import { InjectionToken } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { CommunicationDefinition, serializerDeserializer } from '@backbase/communication-property';
import { Observable, combineLatest } from 'rxjs';
import { filter, first, map } from 'rxjs/operators';

import { Ó¨RootContainerService } from '../../../models/root-container.service';
import { Ó¨ItemNavigationService } from '../../item-navigation.service';
import { PipelineSink, PipelineSource, Sink, Source } from '../pipeline-interface';
import { createPipelineProcessorConfig, PipelineProcessorConfig } from '../pipeline-registry';

/**
 * @deprecated Will be removed in v7.0.0
 */
export interface RouteParams {
  [key: string]: any;
}

/**
 * @deprecated Will be removed in v7.0.0
 * Use the router to store data.
 *
 * ```
 * [ Component Output ] -> [ Route Store ]
 * [ Route Store ] -> [ Component Input ]
 * ```
 *
 * See `RouteParam` for configuration.
 *
 * Note: you can use a single output to save multiple params
 * to a store by using `RouteParamsMultiSink`.
 */
export interface RouteParamsStore<T extends RouteParamPayload> extends PipelineSource<T | undefined>, PipelineSink<T> {
  /**
   * @param paramName The name of the param to save in the route
   */
  source: (group: string) => Source<T | undefined>;
  /**
   * @param paramName The name of the param to read from the route
   */
  sink: (group: string) => Sink<T>;
}

/**
 * @deprecated Will be removed in v7.0.0
 * Pipeable operator for normalizing value to `RouteParams`.
 *
 * @param paramNameArg
 */
export const routeParams = (paramNameArg: string) => (stdin: Observable<string | undefined>) =>
  stdin.pipe(map((payload: string | undefined) => (paramNameArg ? { [paramNameArg]: `${payload}` } : {})));

const TAG: 'route-param' = 'route-param';

/**
 * @deprecated Will be removed in v7.0.0
 * RouteParam Communication Transport
 *
 * Can be used to configure communication using RouteParam.
 *
 * @usageNotes
 *
 * Store some 2-way bound state to the router as "paramName"
 *
 * ```json
 * {
 *   "name": "widget-a",
 *   "properties": {
 *     "output.myOutput": RouteParam.toProperty("paramName"),
 *     "input.myInput": RouteParam.toProperty("paramName")
 *   }
 * }
 * ```
 *
 * Navigate to widget-b and pass data via route-param
 *
 * ```json
 * {
 *   "name": "widget-a",
 *   "properties": {
 *     "output.myOutput": RouteParam.toProperty("inputName")
 *   }
 * },
 * {
 *   "name": "widget-b",
 *   "properties": {
 *     "input.myInput": RouteParam.toProperty("inputName")
 *   }
 * }
 * ```
 */
export class RouteParam {
  /**
   * Generate a serialized communication configuration suitable for use as a Widget Input or Output property value.
   *
   * @param group The name of the communication group to which the Input/Output belongs
   */
  static toProperty(group: string): string {
    return serializerDeserializer.toProperty(new CommunicationDefinition(TAG, group));
  }
}

const getActivatedRouteParam = (paramName: string) => (
  activatedRoute: ActivatedRoute,
): Observable<string | undefined> =>
  activatedRoute.paramMap.pipe(
    filter<ParamMap>(Boolean),
    map((params: ParamMap) => params.get(paramName)),
    map(param => (param === null ? undefined : param)),
  );

const walkRoutePath = (activatedRoute: ActivatedRoute): Array<ActivatedRoute> => {
  if (!activatedRoute.parent) {
    return [activatedRoute];
  }
  return [activatedRoute, ...walkRoutePath(activatedRoute.parent)];
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export type RouteParamPayload =
  | string
  | {
      [key: string]: string | number;
    };
/**
 * @deprecated Will be removed in v7.0.0
 */
export const getRouteParam = (activatedRoute: ActivatedRoute, group: string): Observable<string | undefined> => {
  const activatedRoutes: Array<ActivatedRoute> = walkRoutePath(activatedRoute);
  const allParams$: Array<Observable<string | undefined>> = activatedRoutes.map(getActivatedRouteParam(group));

  return combineLatest(allParams$).pipe(map(allParams => allParams.find(v => v !== undefined)));
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export const createRouteParams = <T extends RouteParamPayload = string>(
  activatedRoute: ActivatedRoute,
  itemNavigation: Ó¨ItemNavigationService,
  rootModel: Ó¨RootContainerService,
): RouteParamsStore<T> => ({
  source: (group: string) => () => getRouteParam(activatedRoute, group).pipe(map(deserialize)),
  sink: (group: string) => {
    return stdin => {
      stdin.pipe(map(serialize), routeParams(group)).subscribe(params => {
        rootModel
          .commonAncestor(new CommunicationDefinition(TAG, group))
          .pipe(first())
          .subscribe(commonAncestor => {
            if (commonAncestor === undefined) {
              return console.warn('No Inputs configured to receive route param');
            }
            itemNavigation.navigateToItem(commonAncestor.name, params);
          });
      });
    };
  },
});
/**
 * @deprecated Will be removed in v7.0.0
 */
export const routeParamsProcessorConfig: PipelineProcessorConfig = createPipelineProcessorConfig(TAG, {
  provide: new InjectionToken('Route param pipeline handler'),
  useFactory: createRouteParams,
  deps: [ActivatedRoute, Ó¨ItemNavigationService, Ó¨RootContainerService],
});

function serialize<T extends RouteParamPayload>(outputValue: T): string {
  return typeof outputValue === 'string' ? outputValue : `"${JSON.stringify(outputValue)}"`;
}

function deserialize(payload: string | undefined) {
  if (payload === undefined) {
    return undefined;
  }

  // not serialized
  if (!(payload.startsWith('"') && payload.endsWith('"'))) {
    return payload;
  }

  // actually attempt to deserialize
  try {
    return JSON.parse(payload.substr(1, payload.length - 2));
  } catch (_) {
    return payload;
  }
}
