import { Injectable } from '@angular/core';
import { Flow, FlowsService } from './flows.service';
import { Ó¨ItemNavigationService } from '../item-navigation.service';
import { ItemModel } from '../../models/item-model';

@Injectable()
export class ItemTransitionService {
  constructor(private readonly flows: FlowsService, private readonly navigationService: Ó¨ItemNavigationService) {}

  transition(
    fromItem: ItemModel,
    fromOutput: string,
    toItem: ItemModel,
    toInput: string | undefined,
    ancestors: Array<ItemModel>,
    payload: any,
  ): Promise<boolean> {
    // Deprecated
    const flows = this.flows.getFlows(
      fromItem.ÉµclassId,
      fromOutput,
      [...ancestors.map(item => item.ÉµclassId)],
      toInput,
    );
    if (flows.length === 0) {
      flows.push({ output: fromOutput, input: toInput });
    }
    const params = this.mapFlows(flows, toInput, payload);
    return this.navigationService.navigateToItem(toItem.name, params);
  }

  private mapFlows(flows: Array<Flow>, toInput: string | undefined, payload: any): any {
    return flows.reduce((acc, flow) => {
      const inputName = toInput || flow.input || flow.output;
      const value = flow.map ? flow.map(payload) : payload;
      if (value === undefined) {
        return acc; // @todo: should it be possible to "clear" a param with undefined?
      }
      // Note: if 2 flows map to the same input, the 2nd will overwrite
      return {
        ...acc,
        [inputName]: value,
      };
    }, {});
  }
}
