import { Injectable, Inject, InjectionToken, Optional, isDevMode } from '@angular/core';
import { of, Observable } from 'rxjs';
import { PAGE_CONFIG, PageConfig } from '@backbase/foundation-ang/web-sdk';

/**
 * @deprecated Will be removed in v7.0.0
 */
export interface AssetsConfiguration {
  /**
   * The name of the CX item containing the assets.
   *
   * This allows the assets to be packaged and served from one CX item, generally the app container item itself.
   */
  assetsStaticItemName: string;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export type AssetIDResolver = (assetId: string) => string;
/**
 * @deprecated Will be removed in v7.0.0
 * The method resolves an asset from a configured on the App level source
 * @param assetId id of the asset item to be resolved
 */
export type AssetsResolver = (assetId: string) => Observable<string>;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface AssetsResolverConfig {
  widgetName: string;
  getURIFromAssetID: AssetIDResolver;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export const CONFIG_ASSETS = new InjectionToken<AssetsConfiguration>(
  '@backbase/foundation Assets Service Configuration',
);
const DEFAULT_STATIC_ROOT = '/';
const DEFAULT_STATIC_ITEM_NAME = '';
/**
 * @deprecated Will be removed in v7.0.0
 */
@Injectable()
export class AssetsService {
  constructor(
    @Optional()
    @Inject(PAGE_CONFIG)
    readonly pageConfig: PageConfig | null,
    @Optional()
    @Inject(CONFIG_ASSETS)
    readonly assetsConfig: AssetsConfiguration | null,
  ) {
    if (!pageConfig && isDevMode()) {
      console.warn(
        `pageConfig is not provided which may impact the path that is resolving assets
         please include *either* the 'WebSdkModule.forRoot(...)' in your AppModule (for CDN deployment) or WebSdkApiModule
          (for CX deployment)`,
      );
    }
  }

  public createAssetsResolver(config: AssetsResolverConfig): AssetsResolver {
    const widgetName = config.widgetName;
    const assetsIdResolver = config.getURIFromAssetID;
    const staticRoot = this.pageConfig ? this.pageConfig.staticResourcesRoot : DEFAULT_STATIC_ROOT;
    const staticItemName = this.assetsConfig ? this.assetsConfig.assetsStaticItemName : DEFAULT_STATIC_ITEM_NAME;

    return (assetId: string): Observable<string> => {
      if (!assetId.length) {
        return of('');
      }
      return of(
        [staticRoot, staticItemName, 'assets', widgetName, assetsIdResolver(assetId)]
          .filter(Boolean)
          .map(str => str.replace(/\/$/, ''))
          .join('/'),
      );
    };
  }
}
