import { Properties, Property } from '@backbase/foundation-ang/web-sdk';
import { Observable } from 'rxjs';
import { distinctUntilChanged, map, pluck, scan, shareReplay, startWith } from 'rxjs/operators';
import { equal, propertiesByPrefix, propertiesAsPairs } from '../../web-sdk/properties';
import { ObservableTree } from './tree';

export type ItemName = string;

/**
 * @deprecated Will be removed in v7.0.0
 */
export class ItemModel {
  /**
   * Name of the page instance of Backbase Item
   */
  readonly name: ItemName;
  /**
   * Properties of Backbase Item
   */
  readonly properties: Observable<Properties>;

  /**
   * The classId of the component that will render this item.
   * @internal
   */
  readonly ÉµclassId: string;

  /**
   * The property definitions for this item.
   * @internal
   */
  readonly Éµproperties: Properties;

  /**
   * The input handlers for this item.
   * @internal
   */
  readonly ÉµinputProperties: Properties;

  /**
   * The output handlers for this item.
   * @internal
   */
  readonly ÉµoutputProperties: Properties;

  constructor(name: ItemName, initialProperties: Properties, propertyUpdates: Observable<Properties>) {
    this.name = name;

    const applyPropertyUpdate = (currentProperties: Properties, update: Properties): Properties => {
      return { ...currentProperties, ...update };
    };

    this.properties = propertyUpdates.pipe(startWith(initialProperties), scan(applyPropertyUpdate), shareReplay(1));

    if (initialProperties.classId === undefined) {
      throw new Error(`Missing classId property for ${name}`);
    }

    this.ÉµclassId = initialProperties.classId as string;
    this.ÉµoutputProperties = this.outputProperties(initialProperties);
    this.ÉµinputProperties = this.inputProperties(initialProperties);
    this.Éµproperties = this.nonIOProperties(initialProperties);
  }

  property<T extends Property>(propertyName: string, defaultValue: T): Observable<T>;
  property<T extends Property>(propertyName: string): Observable<T | undefined>;
  property(propertyName: string, defaultValue?: Property): Observable<Property | undefined> {
    return this.properties.pipe<Property | undefined, Property | undefined, Property | undefined>(
      pluck(propertyName),
      map(propertyValue => (propertyValue === undefined || propertyValue === null ? defaultValue : propertyValue)),
      distinctUntilChanged((a, b) => {
        if (a === undefined && b === undefined) {
          return true;
        }
        if (a === undefined) {
          return false;
        }
        if (b === undefined) {
          return false;
        }
        return equal(a, b);
      }),
    );
  }

  private outputProperties(properties: Properties): Properties {
    return propertiesByPrefix('output.', properties);
  }

  private inputProperties(properties: Properties): Properties {
    return propertiesByPrefix('input.', properties);
  }

  private nonIOProperties(properties: Properties): Properties {
    return propertiesAsPairs(
      pairs => pairs.filter(([name]) => !name.startsWith('input.') && !name.startsWith('output.')),
      properties,
    );
  }
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export class ItemModelTree extends ObservableTree<ItemModel> {}
