import { ComponentRef, Injector } from '@angular/core';
import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpErrorResponse,
  HttpClient,
} from '@angular/common/http';

import { Observable, throwError, race } from 'rxjs';
import { switchMap, catchError, map, take } from 'rxjs/operators';
import { StepUpService, StepUpComponent, StepUpComponentType } from './stepup.service';
/**
 * @deprecated Will be removed in v7.0.0
 */
export type ShouldInterceptStepUpCallback = (response: HttpErrorResponse) => boolean;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface ShouldInterceptStepUpConfig {
  /**
   * Step-up authentication is triggered when the response `statusCode` matches the one defined by this option.
   * Default: `401`.
   */
  statusCode: number;
  /**
   * Step-up authentication is triggered when this header is present in a error response in combination with `statusCode` check
   * Default: `WWW-Authenticate`.
   */
  headerAttribute: string;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export const createInterceptCallback = (
  interceptConfig: ShouldInterceptStepUpConfig,
): ShouldInterceptStepUpCallback => (responseError: HttpErrorResponse): boolean => {
  return (
    responseError.status === interceptConfig.statusCode &&
    responseError.headers.get(interceptConfig.headerAttribute) !== null
  );
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export const isInterceptCallback = (
  interceptConfig: ShouldInterceptStepUpCallback | ShouldInterceptStepUpConfig,
): interceptConfig is ShouldInterceptStepUpCallback => typeof interceptConfig === 'function';

/**
 * @deprecated Will be removed in v7.0.0
 * The step-up interceptor configuration interface.
 * Step-up authentication provides a higher level of assurance that
 * a person is who they claim to be. When a user tries to perform a task,
 * they are challenged to produce additional forms of authentication.
 */
export interface StepUpInterceptorConfig {
  /**
   * Configuration for the retry requests of the stepup authentication mechanism.
   */
  retryRequests: {
    /**
     * The key of the header which is added to the retry request.
     * The value for this header will be determined by the payload of an event
     * emitted by the `StepUpComponentType.complete`. See the `StepUpComponentType` interface.
     * Default: `X-MFA`
     */
    headerAttribute: string;
  };
  /**
   * If this header is present, on a error response and it matches statusCode, it will trigger a step-up mechanism
   */
  interceptResponses: ShouldInterceptStepUpCallback | ShouldInterceptStepUpConfig;
}

/**
 * @deprecated Will be removed in v7.0.0
 * Configuration interface for the step-up authentication.
 */
export type StepUpConfig = StepUpInterceptorConfig & {
  /**
   * Step-up component class. When a step-up request is intercepted this is the
   * component which will be displayed to resolve the step-up authentication.
   */
  mediatorComponentClass: StepUpComponentType;
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export const DEFAULT_STEPUP_CONFIG: StepUpInterceptorConfig = {
  retryRequests: {
    headerAttribute: 'X-MFA',
  },
  interceptResponses: {
    statusCode: 401,
    headerAttribute: 'WWW-Authenticate',
  },
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export type StepupComponentCompleteEvent = ['complete', string];
/**
 * @deprecated Will be removed in v7.0.0
 */
export type StepupComponentCancelEvent = ['cancel', undefined];
/**
 * @deprecated Will be removed in v7.0.0
 */
export type StepupComponentEvents = StepupComponentCancelEvent | StepupComponentCompleteEvent;
/**
 * @deprecated Will be removed in v7.0.0
 */
export class StepUpInterceptor implements HttpInterceptor {
  /**
   * @deprecated Should not be public
   * @todo convert to a local variable within  the intercept handler
   */
  mediatorComponent: ComponentRef<StepUpComponent> | undefined;

  constructor(
    private readonly stepupService: StepUpService,
    private readonly config: StepUpConfig,
    private readonly injector: Injector,
  ) {}

  intercept<R, T>(req: HttpRequest<R>, next: HttpHandler): Observable<HttpEvent<T>> {
    const shouldIntercept: ShouldInterceptStepUpCallback = isInterceptCallback(this.config.interceptResponses)
      ? this.config.interceptResponses
      : createInterceptCallback(this.config.interceptResponses);
    return next.handle(req).pipe(
      catchError((error: HttpErrorResponse): Observable<never> | Observable<HttpEvent<T>> => {
        if (!shouldIntercept(error)) {
          // if it's an error which shouldn't be intercepted propagate the error
          // to as if it wasn't intercepted at all.
          return throwError(error);
        }
        this.mediatorComponent = this.mediatorComponent ?? this.stepupService.stepupComponent;

        // Errors which are to be intercepted should be "paused" and the stream will be handled according
        // to the event coming from a mediator component. On complete event we are going to retry the request
        // while if any other event was fired by the mediator component, we proceed with the error as usual
        const handleMediatorComponentEvent = ([event, token]: StepupComponentEvents): Observable<HttpEvent<T>> => {
          if (event === 'complete' && token) {
            // The HttpClient cannot be injected because it is a circular dependency, so we must lazily get it only when required.
            const http = this.injector.get<HttpClient>(HttpClient);
            return http.request<T>(req.clone({ setHeaders: { [this.config.retryRequests.headerAttribute]: token } }));
          } else {
            return throwError(error);
          }
        };

        // we listen to an event from mediator component in order to be able to amend
        // requests "chain", so we can repeat the request or just propagate the error
        const mediatorComponentResponse = race<StepupComponentEvents>(
          this.mediatorComponent.instance.complete.pipe(
            map<string, StepupComponentCompleteEvent>(value => ['complete', value]),
          ),
          this.mediatorComponent.instance.cancel.pipe(
            map<any, StepupComponentCancelEvent>(() => ['cancel', undefined]),
          ),
        ).pipe(take(1), switchMap(handleMediatorComponentEvent));

        // and we trigger the component to open
        this.mediatorComponent.instance.open(error);
        return mediatorComponentResponse;
      }),
    );
  }
}
