import { InjectionToken } from '@angular/core';
import { Observable, combineLatest } from 'rxjs';
import { PipelineProcessorConfig } from '../pipeline-registry';
import { ItemTransitionService } from '../../deprecated/item-transition.service';
import { PipelineSink } from '../pipeline-interface';
import { ItemModel, ItemModelTree } from '../../../models/item-model';
import { Ó¨RootContainerService } from '../../../models/root-container.service';
import { mergeMap, filter, map } from 'rxjs/operators';

export const pipeName = 'navigation';

export const navigationHandlerToken = new InjectionToken('Navigation handler');

export const createNavigationSink = (
  rootContainer: Ó¨RootContainerService,
  itemTransition: ItemTransitionService,
  itemModel: ItemModel,
): PipelineSink<string | undefined> => {
  return {
    sink: (outputName?: string, config?: string) => (stdin: Observable<string | undefined>) => {
      if (outputName === undefined || config === undefined) {
        return;
      }
      const [targetName, targetInput] = config.split('.') as [string, string | undefined];

      const target$: Observable<ItemModelTree> = rootContainer.model.pipe(
        mergeMap(tree => tree.find(item => item.name === targetName)),
        filter((a: ItemModelTree | undefined): a is ItemModelTree => a !== undefined),
      );
      const targetAnscestors: Observable<Array<ItemModel>> = target$.pipe(
        mergeMap(tree => tree.flatten()),
        map(flatNodes => flatNodes.map(node => node.value)),
      );
      combineLatest([target$, targetAnscestors, stdin]).subscribe(([target, anscestors, payload]) =>
        itemTransition.transition(itemModel, outputName, target.value, targetInput, anscestors, payload),
      );
    },
  };
};

export const navigationProcessorConfig: PipelineProcessorConfig = {
  name: pipeName,
  provider: {
    provide: navigationHandlerToken,
    useFactory: createNavigationSink,
    deps: [Ó¨RootContainerService, ItemTransitionService, ItemModel],
  },
};
