export interface Edge<S> {
  value: S;
}

/**
 * @deprecated
 */
export class Graph<T, S> {
  public vertexes: Set<T> = new Set();
  public edges: Map<T, Map<T, Edge<S>>> = new Map();

  public add(vertex: T): void {
    this.vertexes.add(vertex);
  }

  public connect(from: T, to: T, value: S) {
    this.add(from);
    this.add(to);

    let vertexEdges = this.edges.get(from);
    if (vertexEdges === undefined) {
      vertexEdges = new Map();
      this.edges.set(from, vertexEdges);
    }
    vertexEdges.set(to, { value });
  }

  public getEdge(from: T, to: T): S | undefined {
    const edges = this.edges.get(from);
    const edge = edges === undefined ? undefined : edges.get(to);
    return edge === undefined ? undefined : edge.value;
  }
}
