import { Route, Routes } from '@angular/router';
import { RouteComponent } from '../components/route.component';
import { RoutingStrategy } from './routing-decorators';
import { ItemRoutes } from '../config/routing-tree';
import { RoutableItem } from '../config/routable-item-tree';

export class DefaultContainerStrategy implements RoutingStrategy {
  private itemRoutePath(route: string | undefined, index: number): string {
    if (route) {
      return route;
    }
    return (index + 1).toString();
  }

  // routes is guaranteed to have 1 item, but I don't know how to type that
  private defaultRoute(routes: Routes): Route {
    const defaultRoute = routes[0];
    // Default behaviour, redirect to the first tab (primary route).
    return {
      path: '',
      redirectTo: defaultRoute.path,
      pathMatch: 'full',
    };
  }

  private itemRoute(child: RoutableItem, childrenRoutes: Routes, index: number): Route {
    return {
      path: this.itemRoutePath(child.route, index),
      component: RouteComponent,
      canActivate: child.canActivate,
      data: {
        renderChild: child.name,
        custom: child.routeData,
      },
      children: childrenRoutes,
    };
  }

  private findChildrenRoutes(childrenItemRoutes: Array<ItemRoutes>, item: RoutableItem): Routes {
    const itemRoutes = childrenItemRoutes.find(child => child.item === item.name);
    return itemRoutes ? itemRoutes.routes : [];
  }

  private childrenRoutes(children: Array<RoutableItem>, childrenItemRoutes: Array<ItemRoutes>): Routes {
    return children.map((child, index) =>
      this.itemRoute(child, this.findChildrenRoutes(childrenItemRoutes, child), index),
    );
  }

  getRoutes(item: RoutableItem, children: Array<RoutableItem>, childrenRoutes: Array<ItemRoutes>): Routes {
    const childRoutes = this.childrenRoutes(children, childrenRoutes);
    const defaultRoutes = childRoutes.length && item.hasDefault ? [this.defaultRoute(childRoutes)] : [];
    const routes: Routes = [...childRoutes, ...defaultRoutes];
    if (item.canActivateChild.length) {
      return [
        {
          path: '',
          canActivateChild: item.canActivateChild,
          children: routes,
        },
      ];
    } else {
      return routes;
    }
  }
}
