import {
  Component,
  ChangeDetectionStrategy,
  Optional,
  OnInit,
  OnDestroy,
  NgZone,
  ViewChild,
  ViewContainerRef,
  Inject,
} from '@angular/core';
import { flatMap } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { ItemModel } from '../../models/item-model';
import { ObservableTree } from '../../models/tree';
import { Ó¨RootContainerService } from '../../models/root-container.service';
import { Ó¨ItemNavigationService } from '../../communication/item-navigation.service';
import { StepUpService } from '../../stepup/stepup.service';
import { SessionTimeoutService } from '../../session-timeout/session-timeout.service';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';

@Component({
  selector: 'bb-root',
  template: `
    <bb-area [children]="children | async"></bb-area>
    <template #stepupContainer></template>
    <template #sessionTimeoutContainer></template>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RootComponent implements OnInit, OnDestroy {
  @ViewChild('stepupContainer', { read: ViewContainerRef, static: true })
  readonly stepupContainer: ViewContainerRef | undefined;
  @ViewChild('sessionTimeoutContainer', { read: ViewContainerRef, static: true })
  readonly sessionTimeoutContainer: ViewContainerRef | undefined;

  readonly children: Observable<Array<ObservableTree<ItemModel>>> = this.rootContainer.model.pipe(
    flatMap(state => state.children),
  );

  constructor(
    private readonly rootContainer: Ó¨RootContainerService,
    private readonly zone: NgZone,
    private readonly itemNavigation: Ó¨ItemNavigationService,
    @Optional()
    @Inject(PUBSUB)
    private readonly events?: Pubsub,
    @Optional() private readonly stepupService?: StepUpService,
    @Optional() private readonly sessionTimeoutService?: SessionTimeoutService,
  ) {}

  readonly eventListener: EventListener = ({ name }: any) => {
    // [FIXME] This zone should almost certainly be handled in the EventBus, but can't be currently
    // because we only provide an interface not a wrapper
    this.zone.run(() => {
      this.itemNavigation.navigateToItem(name, {});
    });
  };

  ngOnInit() {
    if (this.events) {
      this.events.subscribe('navigation.viewItem', this.eventListener);
    }
    if (this.stepupService && this.stepupContainer) {
      this.stepupService.createStepUpComponent(this.stepupContainer);
    }
    if (this.sessionTimeoutService && this.sessionTimeoutContainer) {
      this.sessionTimeoutService.createSessionTimeoutComponent(this.sessionTimeoutContainer);
    }
  }

  ngOnDestroy() {
    if (this.events) {
      this.events.unsubscribe('navigation.viewItem', this.eventListener);
    }
  }
}
