import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { Properties } from '@backbase/foundation-ang/web-sdk';

@Injectable({
  providedIn: 'root',
})
export class AppConfigService {
  private readonly config = new BehaviorSubject<Properties>({});
  private readonly propertyPrefix = 'AppConfig.';

  get prefix() {
    return this.propertyPrefix;
  }

  getConfig(): Observable<Properties> {
    return this.config.asObservable();
  }

  setConfig(config: Properties) {
    this.config.next(config);
  }
}
