import { ComponentRef } from '@angular/core';
import { Observable } from 'rxjs';
import { PipelineSource, PipelineSink } from '../pipeline-interface';

export const createComponentPipe = <T>(): PipelineSource<T> & PipelineSink<T> => ({
  source: (ref: ComponentRef<any>, outputName: string) => () => ref.instance[outputName],
  sink: (ref: ComponentRef<any>, inputName: string) => (stdin: Observable<T>) => {
    stdin.subscribe((value: any) => {
      ref.instance[inputName] = value;
      ref.changeDetectorRef.markForCheck();
    });
  },
});
