import { Properties } from '@backbase/foundation-ang/web-sdk';
import { inputOutputProperties, InputOutputProperty } from './parse-model';

export function parseProperties(properties: Properties): Array<InputOutputProperty> {
  return inputOutputProperties(properties).filter(prop => !!prop.pipeline && !!prop.pipeline.processorName);
}

export function getProcessorNames(properties: Properties): Array<string> {
  return parseProperties(properties).map(propertyConfig => propertyConfig.pipeline.processorName);
}
