import { PRIMARY_OUTLET } from '@angular/router';
import { Tree } from '../../models/tree';
import { flatMap } from '../../models/array';
import { ItemName } from '../../models/item-model';
import { RoutableItem } from './routable-item-tree';

/**
 * Special type to flag an outlet as having a collision. This
 * enables us to create the outlet tree, and name the outlet, but
 * also flag the item as having a collision with another item.
 */
export class OutletCollision {
  constructor(public outletName: string, public itemName: ItemName, public collisionWith: ItemName) {}
}

export type OutletName = string | OutletCollision;

export interface ItemOutlet {
  name: ItemName;
  outlet?: OutletName;
}

/**
 * A mapping of the component tree, and their outlet configuration.
 * Because this tree isn't flattened, it hasn't had outlet collisions
 * resolved yet.
 */
export type OutletTree = Tree<ItemOutlet>;

/**
 * A flattened tree of only routable items, and their outlets.
 *
 * This is a tree with outlet collisions resolved.
 *
 * Because flattening the tree can make multiple trees, this
 * interface is used to aggregate all the flattened (root) trees.
 */
export interface RoutableOutletTree {
  rootItem: ItemName;
  children: Array<Tree<RoutableItemOutlet>>;
}

export interface RoutableItemOutlet {
  item: ItemName;
  outlet: OutletName;
}

export type OutletMap = Map<ItemName, OutletName>;

const isValidOutlet = (outlet: string | undefined): boolean => outlet !== undefined && outlet !== '';

const outletCollision = (outletItem: RoutableItemOutlet, collisionWith: RoutableItemOutlet): RoutableItemOutlet => {
  return {
    item: outletItem.item,
    outlet: new OutletCollision(`Éµ${outletItem.item}`, outletItem.item, collisionWith.item),
  };
};

const flattenOutletTreeNode = (node: OutletTree): Array<Tree<RoutableItemOutlet>> => {
  const flattenedChildren = flatMap<OutletTree, Tree<RoutableItemOutlet>>(flattenOutletTreeNode, node.children);

  if (node.value.outlet !== undefined) {
    // not routable
    // Don't flatten this node (just it's children)
    return [new Tree({ item: node.value.name, outlet: node.value.outlet }, flattenedChildren)];
  } else {
    // Flatten this node
    return flattenedChildren.reduce((acc, nextChild) => {
      const collisionWith = acc.find(child => child.value.outlet === nextChild.value.outlet);
      const newChild = collisionWith
        ? new Tree(outletCollision(nextChild.value, collisionWith.value), nextChild.children)
        : nextChild;
      return [...acc, newChild];
    }, [] as Array<Tree<RoutableItemOutlet>>);
  }
};

/**
 * Special case for flattening the root node. If it can't be flattened into a tree with a single
 * root node, we create a special root node, so there is only ever a single tree.
 */
const flattenOutletTree = (outletTree: Tree<ItemOutlet>): RoutableOutletTree => {
  return {
    rootItem: outletTree.value.name,
    children: flattenOutletTreeNode(outletTree),
  };
};

const mapItemToOutlet = (item: RoutableItem): ItemOutlet => {
  if (!item.routingStrategy) {
    return {
      name: item.name,
      outlet: undefined,
    };
  }
  return {
    name: item.name,
    outlet: isValidOutlet(item.outlet) ? item.outlet : PRIMARY_OUTLET,
  };
};

const outletTreeToMap = (outletTree: RoutableOutletTree): OutletMap => {
  const entries: Array<[ItemName, OutletName]> = flatMap(tree => tree.flatten(), outletTree.children) // flatten and merge the root outlet trees
    .map((node: Tree<RoutableItemOutlet>): [ItemName, OutletName] => [node.value.item, node.value.outlet]);
  return new Map<ItemName, OutletName>(entries);
};

export const outletMap = (itemTree: Tree<RoutableItem>): OutletMap => {
  return outletTreeToMap(flattenOutletTree(itemTree.map(mapItemToOutlet)));
};
