import { Property, Properties } from '@backbase/foundation-ang/web-sdk';

export function equal(a: Property, b: Property): boolean {
  if (Array.isArray(a) && Array.isArray(b)) {
    if (a.length !== b.length) {
      return false;
    }
    for (let i = 0; i < a.length; i++) {
      if (a[i] !== b[i]) {
        return false;
      }
    }
    return true;
  }
  return a === b;
}

/**
 * Allow the processing of Properties as an array of pairs.
 *
 * @param process The processing to apply to the property pairs; mapping, filtering, etc.
 * @param properties
 */
export const propertiesAsPairs = (
  process: (pairs: Array<[string, Property]>) => Array<[string, Property]>,
  properties: Properties,
): Properties => {
  const pairs = process(Object.entries(properties));
  return pairs.reduce(
    (acc, [name, value]) => ({
      ...acc,
      [name]: value,
    }),
    {} as Properties,
  );
};

/**
 * Returns a subset of the properties that start
 * with a given prefix, and also removes the prefix from the name
 * of the property.
 *
 * @param prefix
 * @param properties
 */
export const propertiesByPrefix = (prefix: string, properties: Properties): Properties =>
  propertiesAsPairs(
    pairs =>
      pairs
        .filter(([name]) => name.startsWith(prefix))
        .map(([name, value]): [string, Property] => [name.substring(prefix.length), value]),
    properties,
  );
