import {
  NgModule,
  ModuleWithProviders,
  ComponentFactoryResolver,
  ANALYZE_FOR_ENTRY_COMPONENTS,
  Injector,
} from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { StepUpInterceptor, StepUpConfig } from './stepup.interceptor';
import { StepUpService, StepUpComponentType, STEPUP_CONFIG } from './stepup.service';
/**
 * @deprecated Will be removed in v7.0.0
 */
export type CustomStepUpConfig = Partial<StepUpConfig> & { mediatorComponentClass: StepUpComponentType };
/**
 * @deprecated Will be removed in v7.0.0
 */
@NgModule({})
export class StepUpModule {
  static forRoot(config: CustomStepUpConfig): ModuleWithProviders<StepUpModule> {
    return {
      ngModule: StepUpModule,
      providers: [
        {
          provide: ANALYZE_FOR_ENTRY_COMPONENTS,
          useValue: [config.mediatorComponentClass],
          multi: true,
        },
        {
          provide: STEPUP_CONFIG,
          useValue: config,
        },
        {
          provide: HTTP_INTERCEPTORS,
          useClass: StepUpInterceptor,
          deps: [StepUpService, STEPUP_CONFIG, Injector],
          multi: true,
        },
        {
          provide: StepUpService,
          useClass: StepUpService,
          deps: [ComponentFactoryResolver, STEPUP_CONFIG],
        },
      ],
    };
  }
}
