import { Observable } from 'rxjs';
import { Routes } from '@angular/router';
import { Injectable } from '@angular/core';

import { ItemModel } from '../../models/item-model';
import { map, switchMap, shareReplay, distinctUntilChanged, withLatestFrom } from 'rxjs/operators';
import { Ó¨RootContainerService } from '../../models/root-container.service';
import { OutletCollision, OutletName, outletMap } from './outlet-map';
import { RouteGuardsRegistryService } from '../guards/route-guards-registry.service';
import { Tree } from '../../models/tree';
import { ItemRegistryService } from '../../rendering/item-registry.service';
import { RoutableItem, routableItemTree } from './routable-item-tree';
import { ComponentRouteTree, routingTree } from './routing-tree';
import { AbstractRouterConfigService } from './abstract-router-config.service';

/**
 * @deprecated Will be removed in v7.0.0
 */
@Injectable({
  providedIn: 'root',
})
export class RouterConfigService extends AbstractRouterConfigService {
  private readonly routableItemTree: Observable<Tree<RoutableItem>> = this.rootContainer.model.pipe(
    switchMap(model => model.toTree()),
    switchMap(model => routableItemTree(this.itemRegistry, this.routeGuardsRegistry, model)),
    shareReplay(1),
  );

  private readonly outlets: Observable<Map<string, OutletName>> = this.routableItemTree.pipe(
    map(outletMap),
    shareReplay(1),
  );

  /**
   * Convert the component tree to a routing tree
   *
   * @internal
   */
  readonly ÉµrouteTree: Observable<ComponentRouteTree> = this.outlets.pipe(
    withLatestFrom(this.routableItemTree),
    map(([outlets, tree]): ComponentRouteTree => routingTree(tree, outlets)),
    shareReplay(1),
  );

  /**
   * The dynamically generated Angular routes for the SPA, based on the model of the app.
   */
  public routes: Observable<Routes> = this.ÉµrouteTree.pipe(
    map((routeTrees: ComponentRouteTree): Routes => routeTrees.value.routes),
    shareReplay(1),
  );

  constructor(
    private readonly itemRegistry: ItemRegistryService,
    private readonly routeGuardsRegistry: RouteGuardsRegistryService,
    private readonly rootContainer: Ó¨RootContainerService,
  ) {
    super();
  }

  /**
   * Get the router-outlet name for the given item in the model tree.
   *
   * @internal
   */
  ÉµitemOutletName(item: ItemModel): Observable<string | undefined> {
    return this.outlets.pipe(
      map(outlets => outlets.get(item.name)),
      map(outlet => (outlet instanceof OutletCollision ? outlet.outletName : outlet)),
      distinctUntilChanged(),
    );
  }
}
