import { Tree, combineTree } from '../../models/tree';
import { ItemModel } from '../../models/item-model';
import { createRoutingStrategy, RoutingStrategy } from '../strategies/routing-decorators';
import { propertiesByPrefix } from '../../../web-sdk/properties';
import { RouteGuardsRegistryService } from '../guards/route-guards-registry.service';
import { Observable, combineLatest, from, of } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ItemRegistryService } from '../../rendering/item-registry.service';
import { Type } from '@angular/core';
import { CanActivate, CanActivateChild, LoadChildrenCallback } from '@angular/router';
import { Properties } from '@backbase/foundation-ang/web-sdk';
import { JourneyComponent } from '../../rendering/chrome/journey.component';

const CUSTOM_DATA_PREFIX = 'route.data.custom.';

export enum RouteModelProperty {
  /**
   * Each child of a RoutableContainer may have a property for "route",
   * else the route will be the index of the child plus 1.
   */
  ROUTE = 'route',
  /**
   * RoutableContainers and RoutableWidget can have an optional "outletName"
   * property to dynamically set the outlet name for the bb-router-outlet.
   */
  OUTLET = 'outletName',
  /**
   * `CanActivate` route guards can be added to generated child routes by including a
   * `route.canActivate` property in the child's model. These guards must be registered with the
   * `RouteGuardsRegistryService` via the `guards` option passed to `BackbaseCore.withConfig`.
   */
  CAN_ACTIVATE = 'route.canActivate',
  /**
   * `CanActivateChild` route guards can be added to generated routes by including a
   * `route.canActivateChild` property in the item's model. These guards must be registered with the
   * `RouteGuardsRegistryService` via the `guards` option passed to `BackbaseCore.withConfig`.
   */
  CAN_ACTIVATE_CHILD = 'route.canActivateChild',
  /**
   * RoutableContainer's can have a "disableDefault" property which will disable the
   * default behaviour of containers where an empty path route will be created to
   * redirect to the first child's route.
   */
  DISABLE_DEFAULT = 'disableDefault',
}

/**
 * @fixme: This is a union of some different types.
 *
 * interface NonRoutable {
 *   name: string;
 *   routingStrategy: undefined;
 * }
 * interface RoutableWidget {
 *   name: string;
 *   routingStrategy: RoutingStrategy;
 *   outletName?: string;
 * }
 * interface RoutableContainer {
 *   name: string;
 *   routingStrategy: RoutingStrategy;
 *   outletName?: string;
 *   hasDefault: boolean;
 * }
 * interface RoutableContainerChild extends NonRoutable | RoutableWidget | RoutableContainer {
 *   route?: string;
 *   routeData: Properties;
 *   canActivate: Array<CanActivate>;
 *   canActivateChild: Array<CanActivateChild>;
 * }
 * type RoutableItem = NonRoutable | RoutableWidget | RoutableContainer | RoutableContainerChild
 */
export interface RoutableItem {
  name: string;
  routingStrategy?: RoutingStrategy;
  hasDefault?: boolean;
  route?: string;
  outlet?: string;
  canActivate: Array<Type<CanActivate>>;
  canActivateChild: Array<Type<CanActivateChild>>;
  routeData: Properties;
}

const getRoutingStrategy = (
  itemRegistry: ItemRegistryService,
  item: ItemModel,
): Observable<RoutingStrategy | undefined> => {
  const loadModuleFunction = itemRegistry.getModuleLoader(item.ÉµclassId);
  if (loadModuleFunction) {
    return of(createJourneyStrategy(item, loadModuleFunction));
  }
  const component = itemRegistry.getComponentValue(item.ÉµclassId);
  if (component) {
    return of(createRoutingStrategy(component));
  }
  return from(itemRegistry.getComponent(item.ÉµclassId)).pipe(map(createRoutingStrategy), startWith(undefined));
};

const getCanActivateRouteGuards = (
  guardRegistry: RouteGuardsRegistryService,
  guards: Array<string>,
): Array<Type<CanActivate>> =>
  guards
    .map(guardName => guardRegistry.canActivate.get(guardName))
    .filter((guard): guard is Type<CanActivate> => guard !== undefined);

const getCanActivateChildRouteGuards = (
  guardRegistry: RouteGuardsRegistryService,
  guards: Array<string>,
): Array<Type<CanActivateChild>> =>
  guards
    .map(guardName => guardRegistry.canActivateChild.get(guardName))
    .filter((guard): guard is Type<CanActivateChild> => guard !== undefined);

const toRoutableItem = (
  item: ItemModel,
  routeGuards: RouteGuardsRegistryService,
  routeProperties: Properties,
  routingStrategy: RoutingStrategy | undefined,
): RoutableItem => {
  return {
    name: item.name,
    routingStrategy,
    route: routeProperties[RouteModelProperty.ROUTE] as string | undefined,
    outlet: routeProperties[RouteModelProperty.OUTLET] as string | undefined,
    canActivate: getCanActivateRouteGuards(
      routeGuards,
      (routeProperties[RouteModelProperty.CAN_ACTIVATE] || []) as Array<string>,
    ),
    canActivateChild: getCanActivateChildRouteGuards(
      routeGuards,
      (routeProperties[RouteModelProperty.CAN_ACTIVATE_CHILD] || []) as Array<string>,
    ),
    hasDefault: routingStrategy ? true !== routeProperties[RouteModelProperty.DISABLE_DEFAULT] : undefined,
    routeData: propertiesByPrefix(CUSTOM_DATA_PREFIX, routeProperties),
  };
};

// @fixme: don't re-emit unless properties we care about change
const routableItem = (itemRegistry: ItemRegistryService, routeGuards: RouteGuardsRegistryService) => (
  item: ItemModel,
): Observable<RoutableItem> =>
  combineLatest([item.properties, getRoutingStrategy(itemRegistry, item)]).pipe(
    map(([routeProperties, routingStrategy]) => toRoutableItem(item, routeGuards, routeProperties, routingStrategy)),
  );

function createJourneyStrategy(item: ItemModel, loadChildren: LoadChildrenCallback): RoutingStrategy {
  return {
    getRoutes() {
      return [
        {
          path: '',
          component: JourneyComponent,
          data: { item },
          loadChildren,
        },
      ];
    },
  };
}

export const routableItemTree = (
  itemRegistry: ItemRegistryService,
  routeGuards: RouteGuardsRegistryService,
  modelTree: Tree<ItemModel>,
): Observable<Tree<RoutableItem>> => combineTree(modelTree.map(routableItem(itemRegistry, routeGuards)));
