import { Injectable, InjectionToken } from '@angular/core';
import { CommunicationDefinition, serializerDeserializer } from '@backbase/communication-property';
import { Observable, ReplaySubject, Subject } from 'rxjs';
import { concatMap, filter, first, map } from 'rxjs/operators';
import { Ó¨RootContainerService } from '../../../models/root-container.service';
import { ItemModel } from '../../../models/item-model';
import { Ó¨ItemNavigationService } from '../../item-navigation.service';
import { PipelineSink, PipelineSource, Sink, Source } from '../pipeline-interface';
import { createPipelineProcessorConfig, PipelineProcessorConfig } from '../pipeline-registry';
import { asyncScheduler } from 'rxjs';

/**
 * @deprecated Will be removed in v7.0.0
 * Use a service to store data and pass it between widgets.
 *
 * ```
 * [ Component Output ] -> [ Memory Store ]
 * [ Memory Store ] -> [ Component Input ]
 * ```
 *
 * See `Memory` for configuration.
 */
export interface MemoryStore<T> extends PipelineSource<T | undefined>, PipelineSink<T> {
  /**
   * @param group The name communication group to connect via the store
   */
  source: (group: string) => Source<T | undefined>;
  /**
   * @param group The name communication group to connect via the store
   */
  sink: (group: string) => Sink<T>;
}

const TAG: 'memory' = 'memory';

/**
 * @deprecated Will be removed in v7.0.0
 * Memory Communication Transport
 *
 * Can be used to configure communication using Memory.
 *
 * @usageNotes
 *
 * Pass some data to widget-b via an in memory store
 *
 * ```json
 * {
 *   "name": "widget-a",
 *   "properties": {
 *     "output.myOutput": Memory.toProperty("myGroupName")
 *   }
 * },
 * {
 *   "name": "widget-b",
 *   "properties": {
 *     "input.myInput": Memory.toProperty("myGroupName")
 *   }
 * }
 * ```
 */
export class Memory {
  /**
   * Generate a serialized communication configuration suitable for use as a Widget Input or Output property value.
   *
   * @param group The name of the communication group to which the Input/Output belongs
   */
  static toProperty(group: string): string {
    return serializerDeserializer.toProperty(new CommunicationDefinition(TAG, group));
  }
}

/**
 * @deprecated Will be removed in v7.0.0
 * Storage service to retain the values for each communication group that is using the Memory transport
 */
@Injectable({
  providedIn: 'root',
})
export class MemoryStorageService {
  private readonly storage = new Map<
    string,
    Subject<{
      value: any;
    }>
  >();
  private getSubject(group: string) {
    if (!this.storage.has(group)) {
      this.storage.set(group, new ReplaySubject(1));
    } // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    return this.storage.get(group)!;
  }

  /**
   * Get an Observable of the values being communicated to the given group
   *
   * @param group The name of the communication group
   */
  get<T>(group: string): Observable<T> {
    return this.getSubject(group)
      .asObservable()
      .pipe(
        map(item => {
          const localValue = item.value;
          if (typeof item.value === 'object' && item.value['_clearPayload'] === true) {
            item.value = undefined;
          }
          return localValue;
        }),
      );
  }

  /**
   * Communicate some value to members of the given communication group
   *
   * @param group The name of the communication group
   * @param value The value to communicate
   */
  set<T>(group: string, value: T): void {
    this.getSubject(group).next({ value });
  }
}

export const createMemory = <T>(
  itemNavigation: Ó¨ItemNavigationService,
  rootModel: Ó¨RootContainerService,
  storage: MemoryStorageService,
): MemoryStore<T> => {
  return {
    source: <U extends T>(group: string) => (): Observable<U | undefined> => storage.get(group),
    sink: <U extends T>(group: string) => (stdin: Observable<U>) => {
      const commonAncestorItem = getItemFormCommunicationGroup(group, rootModel);
      return stdin
        .pipe(concatMap(data => commonAncestorItem.pipe(map(item => [data, item.name] as const))))
        .subscribe(([data, name]) => {
          asyncScheduler.schedule(() => {
            storage.set(group, data);
          });
          itemNavigation.navigateToItem(name, {});
        });
    },
  };
};
/**
 * @deprecated Will be removed in v7.0.0
 */
export const memoryProcessorConfig: PipelineProcessorConfig = createPipelineProcessorConfig(TAG, {
  provide: new InjectionToken('Memory pipeline handler'),
  useFactory: createMemory,
  deps: [Ó¨ItemNavigationService, Ó¨RootContainerService, MemoryStorageService],
});

function getItemFormCommunicationGroup(group: string, rootModel: Ó¨RootContainerService) {
  const communicationDefinition = new CommunicationDefinition(TAG, group);
  const commonAncestorItem = rootModel.commonAncestor(communicationDefinition).pipe(
    first(),
    filter((item): item is ItemModel => {
      if (item === undefined) {
        console.warn(`No Inputs configured to receive in memory value for communication group "${group}"`);
        return false;
      }
      return true;
    }),
  );
  return commonAncestorItem;
}
