import { Injectable } from '@angular/core';
import { CommunicationDefinition, serializerDeserializer } from '@backbase/communication-property';
import { Observable, BehaviorSubject, Subject, of as observableOf, merge, combineLatest } from 'rxjs';
import { map, filter, pluck, shareReplay, switchMap } from 'rxjs/operators';
import { ItemLocation, Item, Properties, Container } from '@backbase/foundation-ang/web-sdk';
import { isContainer } from './item-model-utils';
import { ItemModel } from '../models/item-model';
import { ObservableTree } from '../models/tree';

export const ROOT_COMPONENT_CLASS_ID = 'RootComponent';
const DEFAULT_ROOT_NAME = 'bb-root-empty';

export interface InsertUpdate {
  item: Item;
  itemLocation: ItemLocation;
}
export interface PropertyUpdate {
  name: string;
  properties: Properties;
}
export type RemovalUpdate = ItemLocation;
export type ModelUpdate = InsertUpdate | PropertyUpdate | RemovalUpdate;

export const isInsert = (update: ModelUpdate): update is InsertUpdate =>
  !!((update as InsertUpdate).item && (update as InsertUpdate).itemLocation);

export const isPropertyUpdate = (update: ModelUpdate): update is PropertyUpdate =>
  !!((update as PropertyUpdate).name && (update as PropertyUpdate).properties);

export const isRemovalUpdate = (update: ModelUpdate): update is Exclude<RemovalUpdate, undefined> =>
  !!(update && 'parentName' in update && 'index' in update);

class CommunicationInputs {
  constructor(private readonly properties: Map<string, CommunicationDefinition>) {}

  static fromProperties(properties: Properties): CommunicationInputs {
    return new CommunicationInputs(
      new Map(
        Object.entries(properties)
          .filter((property): property is [string, string] => typeof property[1] === 'string')
          .map(([propertyName, propertyValue]) => [propertyName, serializerDeserializer.fromProperty(propertyValue)])
          .filter((property): property is [string, CommunicationDefinition] => property[1] !== undefined),
      ),
    );
  }

  hasInputFor(communicationDefinition: CommunicationDefinition): boolean {
    return (
      Array.from(this.properties.values()).find(candidate => communicationDefinition.equals(candidate)) !== undefined
    );
  }
}

/**
 * The root container is basically the page model, but we don't assume that the
 * angular app is the entire page. It could just be a subset of the page model,
 * hence we call it "root container"
 *
 * @internal
 */
@Injectable({
  providedIn: 'root',
})
export class Ó¨RootContainerService {
  // The websdk commands mapped to observables.
  private readonly propertyUpdates = new Subject<PropertyUpdate>();
  private readonly inserts = new Subject<InsertUpdate>();
  private readonly removals = new Subject<RemovalUpdate>();

  public readonly modelUpdates: Observable<ModelUpdate> = merge(this.propertyUpdates, this.inserts, this.removals);

  private readonly defaultValue: Container = {
    name: DEFAULT_ROOT_NAME,
    properties: { classId: ROOT_COMPONENT_CLASS_ID },
    children: [],
  };

  private readonly rootContainerItem = new BehaviorSubject<Container>(this.defaultValue);

  public model: Observable<ObservableTree<ItemModel>> = this.rootContainerItem.pipe(
    filter(container => !this.isDefaultContainer(container.name)), // skip the empty state
    map(c => this.createInitialModelState(c)),
    shareReplay(1),
  );

  private createInitialModelState({ name, properties, children }: Container): ObservableTree<ItemModel> {
    const initialRootContainerProperties = {
      ...properties,
      classId: ROOT_COMPONENT_CLASS_ID,
    };
    return this.createModelStateNode({
      name,
      properties: initialRootContainerProperties,
      children,
    });
  }

  private createModelStateNode(item: Item): ObservableTree<ItemModel> {
    // Filter property updates to just the updates for this item.
    const propertyUpdates: Observable<Properties> = this.propertyUpdates.pipe(
      filter(update => update.name === item.name),
      pluck('properties'),
    );

    // Recursively create the children.
    const childNodes: Array<ObservableTree<ItemModel>> = isContainer(item)
      ? item.children.map(this.createModelStateNode.bind(this))
      : [];

    let insertions: Observable<{
      item: ObservableTree<ItemModel>;
      index: number;
    }>;
    let removals: Observable<number>;

    if (isContainer(item)) {
      // Filter container updates for this item (inserts and removes)
      insertions = this.inserts.pipe(
        filter(insert => !!insert.itemLocation && insert.itemLocation.parentName === item.name),
        map(insert => ({
          item: this.createModelStateNode(insert.item),
          index: insert.itemLocation ? insert.itemLocation.index : 0,
        })),
      );

      removals = this.removals.pipe(
        filter((removal: ItemLocation): boolean => !!removal && removal.parentName === item.name),
        pluck('index'),
      );
    } else {
      insertions = observableOf();
      removals = observableOf();
    }

    // Create the node itself.
    return new ObservableTree<ItemModel>(
      new ItemModel(item.name, item.properties, propertyUpdates),
      childNodes,
      insertions,
      removals,
    );
  }

  private get rootName() {
    return this.rootContainerItem.getValue().name;
  }

  private isDefaultContainer(name: string) {
    return name === DEFAULT_ROOT_NAME;
  }

  isBootstrapped() {
    return !this.isDefaultContainer(this.rootName);
  }

  updateItem(name: string, properties: Partial<Properties>) {
    this.propertyUpdates.next({ name, properties: properties as Properties });
  }

  insertItem(item: Item, itemLocation: ItemLocation) {
    if (!this.isBootstrapped()) {
      this.rootContainerItem.next(item as Container);
    } else {
      this.inserts.next({ item, itemLocation });
    }
  }

  removeItem(itemName: string, itemLocation: ItemLocation) {
    if (this.rootName === itemName) {
      this.rootContainerItem.next(this.defaultValue);
    } else {
      this.removals.next(itemLocation);
    }
  }

  private filter(predicate: (value: ItemModel) => boolean): Observable<Array<ObservableTree<ItemModel>>> {
    return this.model.pipe(switchMap(model => model.filter(predicate)));
  }

  private getCommunicationGroupItems(groupDefinition: CommunicationDefinition): Observable<Array<ItemModel>> {
    return this.filter((item: ItemModel) =>
      CommunicationInputs.fromProperties(item.ÉµinputProperties).hasInputFor(groupDefinition),
    ).pipe(map(nodes => nodes.map(node => node.value)));
  }

  // The only case where there will not be a common ancestor (i.e. return `undefined`) is if there are
  // no items with inputs in the communication group.
  commonAncestor(groupDefinition: CommunicationDefinition): Observable<ItemModel | undefined> {
    return combineLatest([
      this.model.pipe(switchMap(model => model.toTree())),
      this.getCommunicationGroupItems(groupDefinition),
    ]).pipe(
      map(([tree, [first, ...items]]) => {
        let lowest: ItemModel | undefined = first;
        for (const item of items) {
          const newLowest = tree.lowestCommonAncestor(lowest, item);
          if (newLowest === undefined) {
            return undefined;
          } else {
            lowest = newLowest;
          }
        }
        return lowest;
      }),
    );
  }
}
