import { InjectionToken, Inject, ValueProvider, FactoryProvider, Type, Injectable } from '@angular/core';
import { flatten } from '../../models/array';

export type ProcessorName = string;

/**
 *
 * @deprecated Will be removed in v7.0.0
 * Configures a provider for a `PipelineProcessor` by name.
 *
 * @usageNotes
 *
 * Register a custom processor:
 *
 * ```typescript
 * BackbaseCoreModule.forRoot({
 *   pipelineProcessors: [{
 *     name: 'custom-pipe',
 *     provider: {
 *       provide: new InjectionToken('Custom handler'),
 *       useValue: {
 *         pipe: () => (stdin: Observable<any>) => stdin.pipe(tap(
 *           (val: any) => console.log(val)
 *         )),
 *       },
 *     },
 *   }],
 * })
 * ```
 *
 * Configure custom processor in model:
 *
 * ```json
 * {
 *   "name": "my-widget",
 *   "properties": {
 *     "output.myOutput": "custom-pipe"
 *   }
 * }
 * ```
 */
export interface PipelineProcessorConfig<T extends ProcessorName = ProcessorName> {
  /**
   * The name of the processor, which can be used in the model.
   *
   * Note: the name must begin with a letter (a-z).
   */
  name: T;
  /**
   * A valid angular provider that will be instantiated when it is needed.
   */
  provider: PipelineProcessorProvider;
}

/**
 * @deprecated Will be removed in v7.0.0
 * See https://angular.io/api/core/StaticProvider
 */
export declare interface StaticClassProvider {
  useClass: Type<any>;
  deps: Array<any>;
  provide: any;
  multi?: boolean;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export type PipelineProcessorProvider = StaticClassProvider | FactoryProvider | ValueProvider;
/**
 * @deprecated Will be removed in v7.0.0
 */
export const CONFIG_PIPELINE_PROCESSORS = new InjectionToken<Array<PipelineProcessorConfig>>(
  '@backbase/foundation Pipeline Handlers',
);

/**
 * @deprecated Will be removed in v7.0.0
 * Helper service wrapping around CONFIG_PIPELINE_PROCESSORS
 */
@Injectable({
  providedIn: 'root',
})
export class PipelineRegistry {
  private readonly registeredHandlers: Map<ProcessorName, PipelineProcessorProvider>;

  constructor(
    @Inject(CONFIG_PIPELINE_PROCESSORS)
    processorsNested: // eslint-disable-next-line @typescript-eslint/array-type
    PipelineProcessorConfig[][], // Angular doesn't like nested array types
  ) {
    const allProcessors: Array<PipelineProcessorConfig> = flatten(processorsNested);
    this.registeredHandlers = new Map(
      allProcessors.map(({ name, provider }): [string, PipelineProcessorProvider] => [name, provider]),
    );
  }

  getProcessorProvider(name: string): PipelineProcessorProvider {
    const provider = this.registeredHandlers.get(name);
    if (provider === undefined) {
      throw new Error(`No provider to pipeline handler ${name}`);
    }
    return {
      deps: [],
      ...provider,
    };
  }

  getProcessorProviderToken(name: string): any {
    return this.getProcessorProvider(name).provide;
  }
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function createPipelineProcessorConfig<T extends ProcessorName>(
  name: T,
  provider: PipelineProcessorProvider,
): PipelineProcessorConfig<T> {
  return {
    name,
    provider,
  };
}
