export const map = <T, R>(callbackfn: (value: T) => R, arr: Array<T>): Array<R> => arr.map<R>(callbackfn);

export const flatten = <T>(arr: Array<Array<T>>): Array<T> =>
  arr.reduce((acc: Array<T>, a: Array<T>): Array<T> => [...acc, ...a], [] as Array<T>);

export const flatMap = <T, R>(callbackfn: (value: T) => Array<R>, arr: Array<T>): Array<R> =>
  flatten<R>(map<T, Array<R>>(callbackfn, arr));

export const uniq = <T>(array: Array<T>): Array<T> => Array.from(new Set<T>(array));
export const hasDuplicates = (array: Array<unknown>): boolean => array.length !== uniq(array).length;

/**
 * Creates a new list out of the two supplied by pairing up equally-positioned
 * items from both lists. The returned list is the length of the longer of two
 * lists. If one of the lists is shorter, the values will be set to "undefined".
 *
 * @param left
 * @param right
 */
export const pairs = <T, R>(left: Array<T>, right: Array<R>): Array<[T | undefined, R | undefined]> => {
  const max = Math.max(left.length, right.length);
  let idx = 0;

  const returnZip: Array<[T | undefined, R | undefined]> = [];
  while (idx < max) {
    returnZip[idx] = [left[idx], right[idx]];
    idx++;
  }
  return returnZip;
};

/**
 * This helper checks if the value is array of strings.
 * @param value a value to be evaluated
 * @returns true for array of strings and false for any other values
 */
export const isArrayOfStrings = (value: any): value is Array<string> =>
  Array.isArray(value) && value.every(item => typeof item === 'string');
