import { Injectable, ComponentRef } from '@angular/core';
import { ItemModel } from '../models/item-model';
import { ObservableTree } from '../models/observable-tree.interface';
import { Subject, Observable } from 'rxjs';
import { filter } from 'rxjs/operators';

export enum LifecycleEventKind {
  Create = 'create',
  Remove = 'remove',
}

export interface LifecycleEvent {
  kind: LifecycleEventKind;
  itemModel: ObservableTree<ItemModel>;
  component: ComponentRef<any>;
}

@Injectable({
  providedIn: 'root',
})
export class ItemLifecycleService {
  private readonly lifecycleEvents: Subject<LifecycleEvent> = new Subject();

  onCreate(itemModel: ObservableTree<ItemModel>, component: ComponentRef<any>) {
    this.lifecycleEvents.next({
      kind: LifecycleEventKind.Create,
      itemModel,
      component,
    });
  }

  onRemove(itemModel: ObservableTree<ItemModel>, component: ComponentRef<any>) {
    this.lifecycleEvents.next({
      kind: LifecycleEventKind.Remove,
      itemModel,
      component,
    });
  }

  fromItem(name: string): Observable<LifecycleEvent> {
    return this.lifecycleEvents.pipe(filter((lcEvent: LifecycleEvent) => name === lcEvent.itemModel.value.name));
  }

  whenCreate(name?: string): Observable<LifecycleEvent> {
    const nameFilter = name ? (currentName: string) => currentName === name : () => true;
    return this.lifecycleEvents.pipe(
      filter(
        (lcEvent: LifecycleEvent) =>
          LifecycleEventKind.Create === lcEvent.kind && nameFilter(lcEvent.itemModel.value.name),
      ),
    );
  }

  whenRemove(name?: string): Observable<LifecycleEvent> {
    const nameFilter = name ? (currentName: string) => currentName === name : () => true;
    return this.lifecycleEvents.pipe(
      filter(
        (lcEvent: LifecycleEvent) =>
          LifecycleEventKind.Remove === lcEvent.kind && nameFilter(lcEvent.itemModel.value.name),
      ),
    );
  }
}
