import { Component } from '@angular/core';
import { ActivatedRoute, Route, Data } from '@angular/router';
import { pluck, switchMap, filter } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { ItemModel, ItemModelTree } from '../../models/item-model';
import { ObservableTree } from '../../models/tree';
import { ItemRegistryService } from '../../rendering/item-registry.service';

@Component({
  selector: 'bb-route',
  template: `
    <ng-container *ngIf="item | async as item">
      <router-outlet *ngIf="isModule(item.value); else renderModel"></router-outlet>
      <ng-template #renderModel><bb-chrome [item]="item"></bb-chrome></ng-template>
    </ng-container>
  `,
})
export class RouteComponent {
  item: Observable<ObservableTree<ItemModel>> = this.route.data.pipe(
    pluck<Data, string>('renderChild'),
    switchMap(renderChild => this.tree.find(item => item.name === renderChild)),
    filter((a: ObservableTree<ItemModel> | undefined): a is ObservableTree<ItemModel> => a !== undefined),
  );

  constructor(
    private readonly route: ActivatedRoute,
    private readonly tree: ItemModelTree,
    private itemRegistry: ItemRegistryService,
  ) {}

  static isItemInRoute(itemName: string, route: Route) {
    if (!route.data || !route.data.renderChild) {
      throw new Error(`@backbase/foundation Invalid route for RouteComponent. Missing data renderChild: ${route}`);
    }
    return route.data.renderChild === itemName;
  }

  /**
   * @internal
   */
  isModule(model: ItemModel) {
    return !!this.itemRegistry.getModuleLoader(model.ÉµclassId);
  }
}
