import {
  Component,
  ComponentRef,
  HostBinding,
  Input,
  ViewChild,
  ViewContainerRef,
  OnInit,
  OnDestroy,
} from '@angular/core';

import { ItemModel } from '../../models/item-model';
import { ObservableTree } from '../../models/tree';
import { ItemLifecycleService } from '../item-lifecycle.service';
import { ItemInjectorService } from '../item-injector.service';
import { ReplaySubject, Observable, Subject } from 'rxjs';
import { shareReplay, takeUntil, pairwise, startWith, withLatestFrom, last, switchMap, map } from 'rxjs/operators';
import { ItemLoaderService } from '../item-loader.service';
import { CombinedComponentFactoryResolver } from '../combined-component-factory-resolver';

export type ComponentItemModelTuple = [ComponentRef<any>, ObservableTree<ItemModel>];

/**
 * ChromeComponent represents "chrome element" and
 * implements dynamic components rendering
 */
@Component({
  selector: 'bb-chrome',
  template: ` <ng-container #vc></ng-container> `,
})
export class ChromeComponent implements OnInit, OnDestroy {
  /**
   * Represents a container where View can be attached
   */
  @ViewChild('vc', { read: ViewContainerRef, static: true })
  vcRef: ViewContainerRef | undefined;

  private readonly gc$ = new Subject<void>();
  private readonly itemValue = new ReplaySubject<ObservableTree<ItemModel>>(1);
  private readonly component$: Observable<ComponentRef<any>> = this.itemValue.pipe(
    switchMap(item => this.createComponent(item)),
    shareReplay(1),
  );
  private readonly componentItemModelTuple: Observable<
    ComponentItemModelTuple | [undefined, undefined]
  > = this.component$.pipe(
    withLatestFrom(this.itemValue),
    map(([ref, item]): ComponentItemModelTuple | [undefined, undefined] =>
      ref === undefined ? [undefined, undefined] : [ref, item],
    ),
    startWith<ComponentItemModelTuple | [undefined, undefined]>([undefined, undefined]),
  );

  /**
   * data-chrome attribute instructs CXP to use this component as drop target area
   */
  @HostBinding('attr.data-chrome') name: string | undefined;

  /**
   * Setter (Input) for an item to be bootstrapped
   * @param item Actual item to be bootstrapped
   */
  @Input()
  set item(item: ObservableTree<ItemModel>) {
    this.name = item.value.name;
    this.itemValue.next(item);
  }

  /**
   * ChromeComponent constructor
   * @param componentFactoryResolver Factory which allows chrome component to resolve items
   * @param injectors Injectors map
   * @param components Components Registry Service
   */
  constructor(
    private readonly itemLifecycle: ItemLifecycleService,
    private readonly itemInjector: ItemInjectorService,
    private readonly itemLoader: ItemLoaderService,
    private readonly componentFactoryLoader: CombinedComponentFactoryResolver,
  ) {}

  ngOnInit() {
    // Insert component on create
    this.componentItemModelTuple
      .pipe(pairwise(), takeUntil(this.gc$))
      .subscribe(([[oldComponent, oldItemValue], [component, item]]) => {
        if (!this.vcRef || !component || !item) {
          return;
        }
        if (oldComponent && oldItemValue) {
          this.itemLifecycle.onRemove(oldItemValue, oldComponent);
        }

        this.vcRef.clear();
        this.vcRef.insert(component.hostView);
        this.itemLifecycle.onCreate(item, component);
        component.changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    // Cleanup on destroy
    this.componentItemModelTuple.pipe(takeUntil(this.gc$), last()).subscribe(([component, item]) => {
      if (item && component) {
        this.itemLifecycle.onRemove(item, component);
      }
    });

    this.gc$.next();
    this.gc$.complete();
  }

  /**
   * inner helper function which creates components
   * @param item item to be created
   */
  private createComponent(item: ObservableTree<ItemModel>): Promise<ComponentRef<any>> {
    return this.itemLoader
      .loadComponent(item.value.ÉµclassId)
      .then(() => this.componentFactoryLoader.resolveComponentFactory(item.value.ÉµclassId))
      .then(componentFactory => {
        // FIXME: @deprecated parentInjector (no replacement)
        const injector = this.itemInjector.createInjector(this.vcRef && this.vcRef.parentInjector, item);

        // Create the component
        return componentFactory.create(injector);
      });
  }
}
