import { Inject, Injectable, InjectionToken, Type } from '@angular/core';
import { CanActivate, CanActivateChild } from '@angular/router';

export type RouteGuardKey = string;

export interface RouteGuardsConfig {
  canActivate?: { [key: string]: Type<CanActivate> };
  canActivateChild?: { [key: string]: Type<CanActivateChild> };
}

export const CONFIG_ROUTE_GUARDS = new InjectionToken<RouteGuardsConfig>('@backbase/foundation Route Guards');
/**
 * @deprecated Will be removed in v7.0.0
 */
@Injectable()
export class RouteGuardsRegistryService {
  readonly canActivate: Map<RouteGuardKey, Type<CanActivate>> = new Map();
  readonly canActivateChild: Map<RouteGuardKey, Type<CanActivateChild>> = new Map();

  constructor(@Inject(CONFIG_ROUTE_GUARDS) configs: Array<RouteGuardsConfig>) {
    configs.forEach(handlerMap => {
      Object.entries(handlerMap.canActivate || {})
        .filter((entry): entry is [RouteGuardKey, Type<CanActivate>] => 'canActivate' in entry[1].prototype)
        .forEach(([key, guard]: [RouteGuardKey, Type<CanActivate>]) => {
          this.canActivate.set(key, guard);
        });
      Object.entries(handlerMap.canActivateChild || {}).forEach(
        ([key, guard]: [RouteGuardKey, Type<CanActivateChild>]) => {
          this.canActivateChild.set(key, guard);
        },
      );
    });
  }
}
