import {
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  SimpleChanges,
  TemplateRef,
  ViewContainerRef,
  Directive,
} from '@angular/core';

import { Subscription } from 'rxjs';
import { TemplateRegistry } from './template-registry';

/**
 * @deprecated Will be removed in v7.0.0
 */
export interface CustomTemplateContext<Host, ExtraContext> {
  readonly $implicit: Host;
  context: ExtraContext | undefined;
}

export type CustomTemplateRef<HostRef, Context> = TemplateRef<CustomTemplateContext<HostRef, Context>>;

/**
 *
 * @deprecated Will be removed in v7.0.0
 *
 * @description
 * Every custom template directive should extend this class.
 * See 'How to make widget customizable'
 *
 * @usageNotes
 *
 * ### Example
 * ```
 * import { Component, Directive } from '@angular/core';
 * import { BbTemplate, ItemModel } from '@backbase/foundation-ang/core';
 *
 * @Component({
 *   selector: 'my-widget',
 *   template: `
 *   <li *ngFor="let item of pallete">
 *     My colors:
 *     <ng-container
 *       customItemTemplate
 *       [bbHostRef]="hostRef"
 *       [bbTemplateContext]="item"
 *     ></ng-container>
 *   </li>
 *
 *   <ng-template customItemTemplate let-hostRef let-item="context">
 *     Current name: {{ item.color }}
 *     Hex value: {{ hostRef.getHexColor(item.color) }}
 *   </ng-template>
 *   `,
 * })
 * export class MyWidgetComponent {
 *   readonly hostRef = this;
 *   readonly palette: Array<PaletteItem> = [{
 *     color: 'blue',
 *   }];
 *   readonly hexValues = {
 *     blue: '0000ff',
 *   };
 *
 *   constructor(private itemModel: ItemModel) {}
 *
 *   getHexColor(color: string): string | undefined {
 *     return this.hexValues[color];
 *   }
 * }
 *
 * type PaletteItem {
 *   color: string;
 * };
 *
 * @Directive({
 *   selector: '[customItemTemplate]',
 * })
 * export class customItemTemplate extends BbTemplate<MyWidgetComponent, PaletteItem> {}
 * ```
 */
/* eslint-disable @angular-eslint/directive-class-suffix */
@Directive({})
export abstract class BbTemplate<HostRef, Context> implements OnInit, OnDestroy, OnChanges {
  /**
   * Custom context
   */
  @Input() bbTemplateContext: Context | undefined;

  /**
   * Context of the host component
   */
  @Input() bbHostRef: HostRef | undefined;

  private templateSubscription: Subscription | undefined;

  private customTemplateContext: CustomTemplateContext<HostRef, Context> | undefined;

  constructor(
    private readonly viewContainerRef: ViewContainerRef,
    private readonly registry: TemplateRegistry,
    @Optional() private readonly templateRef: TemplateRef<CustomTemplateContext<HostRef, Context>>,
  ) {}

  /**
   * @internal
   */
  ngOnInit() {
    this.checkContainerHasHostRef();
    this.checkNgTemplateIsNotUsedtoDefineSlot();

    if (this.isContainer) {
      this.waitTemplate();
    } else {
      this.registry.set<CustomTemplateRef<HostRef, Context>>(this.templateId(), this.templateRef);
    }
  }

  private templateId() {
    return this.constructor;
  }

  private checkContainerHasHostRef() {
    if (this.isContainer && this.bbHostRef === undefined) {
      throw new Error(`${this.constructor.name}: Missing required input [bbHostRef]`);
    }
  }

  private checkNgTemplateIsNotUsedtoDefineSlot() {
    if (!this.isContainer && this.bbHostRef) {
      throw new Error(
        `${this.constructor.name}: [bbHostRef] should not be used in conjunction with <ng-template>. Use <ng-container> instead.`,
      );
    }
  }

  /**
   * @internal
   */
  ngOnChanges(changes: SimpleChanges) {
    if (!this.customTemplateContext) {
      return;
    }

    if ('bbTemplateContext' in changes) {
      this.customTemplateContext.context = changes.bbTemplateContext.currentValue;
    }
  }

  private waitTemplate() {
    const templateSubject = this.registry.get<CustomTemplateRef<HostRef, Context>>(this.templateId());

    this.templateSubscription = templateSubject.subscribe(
      (tempalateRef: TemplateRef<CustomTemplateContext<HostRef, Context>>) => {
        this.viewContainerRef.clear();

        this.customTemplateContext = this.createContext();
        const view = tempalateRef.createEmbeddedView(this.customTemplateContext);
        this.viewContainerRef.insert(view);
      },
    );
  }

  /**
   * @internal
   */
  ngOnDestroy() {
    if (this.templateSubscription) {
      this.templateSubscription.unsubscribe();
    }
  }

  private createContext(): CustomTemplateContext<HostRef, Context> {
    if (this.bbHostRef === undefined) {
      throw new Error(`${this.constructor.name}: Missing required input [bbHostRef]`);
    }

    return {
      $implicit: this.bbHostRef,
      context: this.bbTemplateContext,
    };
  }

  private get isContainer(): boolean {
    return !this.templateRef;
  }
}
