import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FeaturesService } from '@backbase/foundation-ang/future';
import { StoreFactory, ÉµStoreBroker, ÉµSTORE_BRANCH, ÉµSTORE_DISPATCH_CHANNEL } from '@backbase/foundation-ang/store';
import { TemplateRegistry } from '../../customizable/template-registry';
import { ItemModel } from '../../models/item-model';

export function journeyItemModelFactory(route: ActivatedRoute) {
  return route.snapshot.data.item;
}

export function storeNameFactory(route: ActivatedRoute): any {
  return route.snapshot.data.item.name;
}

@Component({
  selector: 'bb-journey',
  template: ` <router-outlet></router-outlet> `,
  providers: [
    {
      provide: ItemModel,
      useFactory: journeyItemModelFactory,
      deps: [ActivatedRoute],
    },
    TemplateRegistry,
    {
      provide: ÉµSTORE_BRANCH,
      useFactory: storeNameFactory,
      deps: [ActivatedRoute],
    },
    {
      provide: ÉµSTORE_DISPATCH_CHANNEL,
      useFactory: storeNameFactory,
      deps: [ActivatedRoute],
    },
    {
      provide: StoreFactory,
      useClass: StoreFactory,
      deps: [ÉµStoreBroker, ÉµSTORE_BRANCH, ÉµSTORE_DISPATCH_CHANNEL, FeaturesService],
    },
  ],
})
export class JourneyComponent {}
