import { flatMap, flatten } from './array';
import { LinkedList } from './linked-list';
import { Observable, merge, of, combineLatest } from 'rxjs';
import { map, scan, shareReplay, distinctUntilChanged, switchMap } from 'rxjs/operators';

export type ChildrenUpdateFn<T> = (acc: Array<ObservableTree<T>>) => Array<ObservableTree<T>>;

const treeValue = <T>(t: Tree<T>): T => t.value;

/**
 * Abstract tree representation.
 */
export class Tree<T> {
  constructor(
    /**
     * The value of this node
     */
    public value: T,
    /**
     * The children of this node
     */
    public children: Array<Tree<T>>,
  ) {}

  map<R>(callbackfn: (value: T, oldChildren: Array<T>, newChildren: Array<R>) => R): Tree<R> {
    const newChildren: Array<Tree<R>> = this.children.map(childTree => childTree.map(callbackfn));
    return new Tree(callbackfn(this.value, this.children.map(treeValue), newChildren.map(treeValue)), newChildren);
  }

  /**
   * Flattens the tree into a flat array of tree nodes.
   *
   * The tree is flattened by in order of depth-first-preorder-traversal.
   */
  flatten(): Array<Tree<T>> {
    return [this, ...flatMap(child => child.flatten(), this.children)];
  }

  pathTo(value: T): Array<T> {
    if (this.value === value) {
      return [this.value];
    } else {
      for (const child of this.children) {
        const childPath = child.pathTo(value);
        if (childPath.length !== 0) {
          return [this.value, ...childPath];
        }
      }
      return [];
    }
  }

  lowestCommonAncestor(left: T, right: T): T | undefined {
    const leftPath = new Set(this.pathTo(left));
    return this.pathTo(right)
      .reverse()
      .find(candidate => leftPath.has(candidate));
  }
}

/**
 * @description
 * This is an operator that can be used with an Observable's `pipe`.
 *
 * The piped observable should be an observable of an array.
 *
 * The source array is mapped over with the provided callback function, and the
 * callback function should take an item (T) from the source array, and return an
 * *observable* of a new item (R).
 *
 * The result is a new observable of an array the same length as the source array
 * but with the Ts mapped to Rs.
 *
 * This is useful for mapping over the observable children of node in the model tree.
 *
 * @usageNotes
 *
 * ### Example
 * ```
 * const children: ObservableTree<ItemModel> = this.model.children;
 * const titleProperties: Observable<Array<string>> = this.model.children.pipe(
 *   mapObservableArray(item => item.value.property<string>('title', 'Default Title'))
 * );
 * ```
 * @param callback Function that produces an *observable* element of the new Array
 */
export const mapObservableArray = function <T, R>(callback: (a: T) => Observable<R>) {
  return (source: Observable<Array<T>>): Observable<Array<R>> => {
    return source.pipe(switchMap((tArray: Array<T>) => combineLatest(tArray.map(callback))));
  };
};

/**
 * An tree where each node's children are observable.
 */
export class ObservableTree<T> {
  // Reducers to update children state
  private readonly initialChildren: Observable<ChildrenUpdateFn<T>> = of((acc: Array<ObservableTree<T>>) => acc); // identity reducer for initial value
  private readonly insertUpdates: Observable<ChildrenUpdateFn<T>> = this.insertions.pipe(
    map((
      { item, index }, // map inserts to reducer functions
    ) => (children: Array<ObservableTree<T>>) => [...children.slice(0, index), item, ...children.slice(index)]),
  );
  private readonly removalUpdates: Observable<ChildrenUpdateFn<T>> = this.removals.pipe(
    map((
      index, // map removals to reducer funtions
    ) => (children: Array<ObservableTree<T>>) => {
      const newChildren = [...children];
      newChildren.splice(index, 1);
      return newChildren;
    }),
  );

  // Combine reducers to a single stream
  private readonly reducers: Observable<ChildrenUpdateFn<T>> = merge(
    this.initialChildren,
    this.insertUpdates,
    this.removalUpdates,
  );

  /**
   * The children of this node
   */
  readonly children: Observable<Array<ObservableTree<T>>>;

  constructor(
    /**
     * The value of this node
     */
    public value: T,
    initialChildren: Array<ObservableTree<T>>,
    private readonly insertions: Observable<{ item: ObservableTree<T>; index: number }>,
    private readonly removals: Observable<number>,
  ) {
    this.children = this.reducers.pipe(
      scan<ChildrenUpdateFn<T>, Array<ObservableTree<T>>>(
        (currentChildren: Array<ObservableTree<T>>, updateFn: ChildrenUpdateFn<T>) => updateFn(currentChildren),
        initialChildren,
      ),
      shareReplay(1),
    );
  }

  /**
   * Find a node in the tree. Returns the result as an observable.
   *
   * If the element is found it is emitted, otherwise, the result emits undefined.
   *
   * The result observable will only emit again if the found node changes (ie: if
   * it is added or removed, or if it finds a different node).
   *
   * @param predicate predicate is called once for each node of the tree, until a
   * node is found. The predicate will be called again every time the tree changes
   * (ie: a node in the tree is added or removed). The predicate is called in order
   * of depth-first-preorder-traversal.
   */
  find(predicate: (value: T) => boolean): Observable<ObservableTree<T> | undefined> {
    return this.flatten().pipe(
      map(nodes => nodes.find(node => predicate(node.value))),
      distinctUntilChanged(),
    );
  }

  pathTo(value: T): Observable<Array<T>> {
    return this.toTree().pipe(
      map((tree: Tree<T>) => tree.pathTo(value)),
      distinctUntilChanged(),
    );
  }

  lowestCommonAncestor(left: T, right: T): Observable<T | undefined> {
    return this.toTree().pipe(
      map((tree: Tree<T>) => tree.lowestCommonAncestor(left, right)),
      distinctUntilChanged(),
    );
  }

  filter(predicate: (value: T) => boolean): Observable<Array<ObservableTree<T>>> {
    return this.flatten().pipe(
      map(nodes => nodes.filter(node => predicate(node.value))),
      distinctUntilChanged(),
    );
  }

  /**
   * Flattens a tree into a single array of nodes. The result nodes are still trees.
   *
   * The tree is flattened in order of depth-first-preorder-traversal.
   *
   * The result is returned as an observable and re-emits every time a node is added
   * or removed from the tree.
   */
  flatten(): Observable<Array<ObservableTree<T>>> {
    return this.children.pipe(
      switchMap(children => (children.length === 0 ? of([]) : combineLatest(children.map(child => child.flatten())))),
      map<Array<Array<ObservableTree<T>>>, Array<ObservableTree<T>>>(flatten),
      map(flat => [this, ...flat]),
    );
  }

  /**
   * Takes a snapshot of the tree.
   */
  toTree(): Observable<Tree<T>> {
    return this.children.pipe(
      switchMap(children => {
        const mappedChildren$ = children.length === 0 ? of([]) : combineLatest(children.map(child => child.toTree()));
        return mappedChildren$.pipe(map((mappedChildren: Array<Tree<T>>) => new Tree(this.value, mappedChildren)));
      }),
    );
  }
}

/**
 * Like combineLatest in rxjs, but for a tree of observables, instead of
 * an array of observables.
 */
export function combineTree<T>(tree: Tree<Observable<T>>): Observable<Tree<T>> {
  // Convert children from Array<Tree<Observable<T>>> to Observable<Array<Tree<T>>>
  const children$: Observable<Array<Tree<T>>> =
    tree.children.length === 0 ? of([]) : combineLatest(tree.children.map(combineTree));
  return combineLatest([tree.value, children$]).pipe(
    map(([value, children]: [T, Array<Tree<T>>]) => new Tree<T>(value, children)),
  );
}

/**
 * Describes a more generic tree than our "Tree" type.
 *
 * This is useful for performing operations on different
 * "tree" implementations.
 *
 * For example, this is type which describes an angular
 * `Route` as our generic tree type:
 * `type AngularRouteTree = GenericTree<Route>;`
 *
 * And this describes our `ComponentRouteTree`
 * `type ComponentTree = GenericTree<Tree<ItemRoutes>>;
 */
export type GenericTree<T> = T & {
  children?: Array<GenericTree<T>>;
};

/**
 * Extracts a branch from a tree that starts at the root, and ends at the leaf
 * node, as determined by the predicate (returns a linked list).
 *
 * If the element is not found, the result is undefined.
 *
 * @param predicate predicate is called once for each node of the tree, until a
 * node is found. The predicate is called in order of depth-first-preorder-traversal.
 */
export const treeBranch = <T>(
  tree: GenericTree<T>,
  predicate: (value: T, index?: number, parent?: T) => boolean,
): LinkedList<T> | undefined => {
  const walkTree = (
    treeInner: GenericTree<T>,
    index?: number,
    parentTree?: GenericTree<T>,
  ): LinkedList<T> | undefined => {
    if (predicate(treeInner, index, parentTree)) {
      return {
        value: treeInner,
      };
    }

    if (!treeInner.children) {
      return undefined;
    }

    for (let i = 0; i < treeInner.children.length; i++) {
      const foundInChild = walkTree(treeInner.children[i], i, treeInner);
      if (foundInChild) {
        return {
          value: treeInner,
          next: foundInChild,
        };
      }
    }

    return undefined;
  };

  return walkTree(tree);
};
