import { ComponentFactoryResolver, ComponentRef, Injectable, StaticProvider, Inject, Optional } from '@angular/core';
import { Observable } from 'rxjs';
import { take, takeUntil } from 'rxjs/operators';

import { ItemModel } from '../../models/item-model';
import { ObservableTree } from '../../models/observable-tree.interface';
import { ItemLifecycleService, LifecycleEvent } from '../../rendering/item-lifecycle.service';
import { LAZY_MAP, LazyConfig, LazyComponentsConfig } from '../../rendering/lazy-config';
import { createComponentPipe } from '../pipes/handlers/component';
import { Pipeline, Sink } from '../pipes/pipeline-interface';
import { PipelineRegistry } from '../pipes/pipeline-registry';
import { PipelineService } from '../pipes/pipeline.service';
import { getProcessorNames, parseProperties } from './broker-helpers';

/**
 * The input broker service creates a communication pipeline
 * using the component input as a sink. The source of the pipeline
 * is determined by the item model.
 */
@Injectable()
export class InputBrokerService {
  constructor(
    private readonly itemLifecycle: ItemLifecycleService,
    private readonly pipelineRegistry: PipelineRegistry,
    private readonly componentFactoryResolver: ComponentFactoryResolver,
    @Inject(LAZY_MAP)
    @Optional()
    private readonly lazyConfig: LazyConfig | null,
  ) {}

  bootstrap() {
    // Model properties cannot be linked to the Inputs of lazy loaded items
    const lazyLoadedComponents = (this.lazyConfig
      ? this.lazyConfig.filter(
          (config): config is LazyComponentsConfig => !!(config as LazyComponentsConfig).components,
        )
      : []
    ).reduce((acc, config) => [...acc, ...config.components], [] as Array<string>);

    this.itemLifecycle.whenCreate().subscribe(({ itemModel, component }) => {
      const gc$ = this.itemLifecycle.whenRemove(itemModel.value.name).pipe(take(1));
      this.runInputPipelines(itemModel, component, gc$);
      if (!lazyLoadedComponents.includes(itemModel.value.ÉµclassId)) {
        this.runModelInputs(itemModel, component, gc$);
      }
    });
  }

  private runModelInputs<C>(
    itemModel: ObservableTree<ItemModel>,
    ref: ComponentRef<C>,
    gc$: Observable<LifecycleEvent>,
  ) {
    this.componentFactoryResolver.resolveComponentFactory(ref.componentType).inputs.forEach(input => {
      // Abort if there is already an "input." property with the same name
      if (itemModel.value.ÉµinputProperties[input.templateName] !== undefined) return;
      const modelValue = itemModel.value.Éµproperties[input.templateName];
      if (modelValue === undefined) return;
      const source = itemModel.value.property(input.templateName, modelValue);
      const sink = this.inputSink(ref, input.propName);
      return sink(source.pipe(takeUntil(gc$)));
    });
  }

  private runInputPipelines<C>(
    itemModel: ObservableTree<ItemModel>,
    ref: ComponentRef<C>,
    gc$: Observable<LifecycleEvent>,
  ) {
    const pipelineService = ref.injector.get<PipelineService>(PipelineService);
    parseProperties(itemModel.value.ÉµinputProperties).forEach(inputPipesConfig => {
      const sourceCommand = inputPipesConfig.pipeline;
      const sourceFn = pipelineService.createSource(sourceCommand.processorName, sourceCommand.args);
      const pipeline: Pipeline<any> = {
        source: sourceFn,
        sink: this.inputSink<C>(ref, inputPipesConfig.propertyName),
      };

      pipelineService.runPipeline(pipeline, gc$);
    });
  }

  private inputSink<C>(ref: ComponentRef<C>, inputName: string): Sink<any> {
    return createComponentPipe().sink(ref, inputName);
  }

  getItemProviders(item: ItemModel): Array<StaticProvider> {
    return getProcessorNames(item.ÉµinputProperties).map(processorName => {
      try {
        return this.pipelineRegistry.getProcessorProvider(processorName);
      } catch (message) {
        throw new Error(`Input '${processorName}' on ${item.name} is misconfigured. ${message}`);
      }
    });
  }
}

export function createInputBootstrap(Broker: InputBrokerService) {
  return Broker.bootstrap.bind(Broker);
}
