import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import { LoadChildrenCallback } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { filter, map, take } from 'rxjs/operators';
import { LazyConfigItem, LazyModuleConfig, LAZY_MAP } from './lazy-config';

export type ComponentClass<T> = new (...args: Array<any>) => T;

export interface ComponentMap {
  [key: string]: ComponentClass<any>;
}

/**
 * ComponentRegistry configuration injection token
 */
export const CONFIG_COMPONENT_CLASS_MAP = new InjectionToken<Map<string, ComponentClass<any>>>(
  '@backbase/foundation Components Registry',
);

/**
 * ItemRegistryService stores a hashMap
 * of the components included into the app and
 * avaliable for usage. It's meant to be an extension
 * point which is used by a developer.
 */
@Injectable()
export class ItemRegistryService {
  private readonly itemRegistry: BehaviorSubject<Map<string, ComponentClass<any>>>;
  private readonly lazyModuleConfig: Array<LazyModuleConfig>;

  /**
   * ItemRegistryService constructor which allows to define components
   * included into application
   * @param ComponentRegistry a hashMap of the component classes to be added
   */
  constructor(
    @Inject(CONFIG_COMPONENT_CLASS_MAP)
    @Optional()
    classMaps: Array<ComponentMap> | undefined,
    @Inject(LAZY_MAP)
    @Optional()
    lazyConfig: Array<LazyConfigItem> | null,
  ) {
    this.itemRegistry = new BehaviorSubject(
      new Map<string, ComponentClass<any>>(
        classMaps
          ? classMaps.reduce(
              (acc: Array<[string, ComponentClass<any>]>, classMap) => [...acc, ...Object.entries(classMap)],
              [],
            )
          : undefined,
      ),
    );

    this.lazyModuleConfig = (lazyConfig || []).filter(isLazyModuleConfig);
  }

  entries() {
    return Array.from(this.itemRegistry.value.entries());
  }

  add(classId: string, componentClass: ComponentClass<any>) {
    this.itemRegistry.next(new Map([...this.entries(), [classId, componentClass]]));
  }

  has(classId: string): boolean {
    return this.itemRegistry.value.has(classId);
  }

  /**
   * This method allows to retrieve Component by it's classId
   * @param classId classId key to get a component class
   * @returns componentType to be used by a ComponentFactoryResolver
   */
  getComponentValue(classId: string): ComponentClass<any> | undefined {
    return this.itemRegistry.value.get(classId);
  }

  getComponent(classId: string): Promise<ComponentClass<any>> {
    return this.itemRegistry
      .pipe(
        map(registry => registry.get(classId)),
        filter((componentClass): componentClass is ComponentClass<any> => componentClass !== undefined),
        take(1),
      )
      .toPromise();
  }

  getModuleLoader(classId: string): LoadChildrenCallback | undefined {
    const lazyModuleConfigItem = this.lazyModuleConfig.find(config => config.module === classId);
    return lazyModuleConfigItem && lazyModuleConfigItem.loadChildren;
  }
}

function isLazyModuleConfig(config: LazyConfigItem): config is LazyModuleConfig {
  return !!(config as LazyModuleConfig).module;
}
