import { Injectable, ComponentFactoryResolver, ComponentFactory } from '@angular/core';
import { ItemRegistryService } from './item-registry.service';

@Injectable()
export class CombinedComponentFactoryResolver {
  private readonly childResolvers = new Map<string, ComponentFactoryResolver>();
  constructor(
    private readonly itemRegistry: ItemRegistryService,
    private readonly rootResolver: ComponentFactoryResolver,
  ) {}

  registerChildResolver(classId: string, resolver: ComponentFactoryResolver) {
    this.childResolvers.set(classId, resolver);
  }

  resolveComponentFactory<T>(classId: string): ComponentFactory<T> {
    const type = this.itemRegistry.getComponentValue(classId);
    if (!type) {
      throw new Error(`@backbase/foundation No component registered for ${classId}. Did you add it to the classMap?`);
    }
    const resolver = this.childResolvers.get(classId) || this.rootResolver;
    return resolver.resolveComponentFactory(type);
  }
}
