import {
  ComponentRef,
  ViewContainerRef,
  ComponentFactoryResolver,
  ComponentFactory,
  Injectable,
  EventEmitter,
  Inject,
  InjectionToken,
} from '@angular/core';
import { StepUpConfig, DEFAULT_STEPUP_CONFIG } from './stepup.interceptor';
import { CustomStepUpConfig } from './stepup.module';
import { HttpErrorResponse } from '@angular/common/http';
/**
 * @deprecated Will be removed in v7.0.0
 */
export const STEPUP_CONFIG = new InjectionToken<StepUpConfig>('Custom step-up configuration injection token');

/**
 * @deprecated Will be removed in v7.0.0
 * The interface which should be implemented by a stepUp component
 */
export interface StepUpComponent {
  /**
   * Indicates that the secret has been provided by the user via the `StepUpComponent`.
   * After it emits the retry request will be sent to the API endpoint.
   */
  complete: EventEmitter<string>;
  /**
   * The StepUpComponent instance emits this cancel event to trigger standard behaviour.
   * That is, there is no retry request and HttpEvent is propagated to the next interceptor in the chain.
   */
  cancel: EventEmitter<any>;
  /**
   * The method of the `StepUpComponent` which is called when a request is intercepted.
   * Trigger this method should show the component where user can provide a second step "secret".
   */
  open: (response: HttpErrorResponse) => void;
}

/**
 * @deprecated Will be removed in v7.0.0
 * The type which defines the StepUpComponent signature
 */
export type StepUpComponentType = new (...args: Array<any>) => StepUpComponent;
/**
 * @deprecated Will be removed in v7.0.0
 */
@Injectable()
export class StepUpService {
  private stepupComponentRef: ComponentRef<StepUpComponent> | undefined;
  private readonly stepupConfig: StepUpConfig;

  constructor(
    private readonly resolver: ComponentFactoryResolver,
    @Inject(STEPUP_CONFIG) stepupConfigParam: CustomStepUpConfig,
  ) {
    this.stepupConfig = { ...DEFAULT_STEPUP_CONFIG, ...stepupConfigParam };
  }

  get stepupComponent(): ComponentRef<StepUpComponent> {
    if (this.stepupComponentRef) {
      return this.stepupComponentRef;
    }
    throw new Error('No stepupComponent defined');
  }

  createStepUpComponent(stepupContainer: ViewContainerRef) {
    stepupContainer.clear();
    const factory: ComponentFactory<StepUpComponent> = this.resolver.resolveComponentFactory(
      this.stepupConfig.mediatorComponentClass,
    );
    this.stepupComponentRef = stepupContainer.createComponent(factory);
  }
}
