import { Injectable } from '@angular/core';
import { ReplaySubject, Observable } from 'rxjs';
import { first } from 'rxjs/operators';

export type Id = any;

@Injectable()
export class TemplateRegistry {
  private readonly registry: Map<Id, ReplaySubject<any>> = new Map();

  set<T>(id: Id, entry: T) {
    const entry$: ReplaySubject<T> = this.registry.get(id) || this.initEntry(id);
    entry$.next(entry);
  }

  get<T>(id: Id): Observable<T> {
    const entry$ = this.registry.get(id) || this.initEntry(id);
    return entry$.pipe(first());
  }

  private initEntry<T>(id: Id): ReplaySubject<T> {
    const entry = new ReplaySubject<T>();
    this.registry.set(id, entry);
    return entry;
  }
}
