import { Injectable, InjectionToken, Inject, Optional } from '@angular/core';
import { Graph } from '../../models/graph';

export type ClassId = string;
export type OutputName = string;
export type InputName = string;

export interface ComponentOutputConfig {
  classId: ClassId;
  outputName: OutputName;
}
export interface ComponentInputConfig {
  classId: ClassId;
  inputName?: InputName;
}

export interface CommunicationFlowConfig {
  output: ComponentOutputConfig;
  input: ComponentInputConfig;
  mapPayload?: (payload: any) => any;
}

export interface Flow {
  output: OutputName;
  input?: InputName;
  map?: (payload: any) => any;
}

export const CONFIG_FLOWS = new InjectionToken<Array<Array<CommunicationFlowConfig>>>(
  '@backbase/foundation Communcation Flow Configuration',
);

@Injectable()
export class FlowsService {
  private readonly transitions = new Graph<ClassId, Array<Flow>>();

  constructor(
    @Optional()
    @Inject(CONFIG_FLOWS)
    flowsConfig: // eslint-disable-next-line @typescript-eslint/array-type
    Array<CommunicationFlowConfig>[] | null, // Angular doesn't like nested array types
  ) {
    if (flowsConfig !== null) {
      flowsConfig.forEach(flows => {
        flows.forEach(flow => {
          this.addFlow(flow);
        });
      });
    }
  }

  private addFlow(config: CommunicationFlowConfig) {
    const currentEdge = this.transitions.getEdge(config.output.classId, config.input.classId);
    const flow = {
      output: config.output.outputName,
      input: config.input.inputName,
      map: config.mapPayload,
    };
    const edge = currentEdge ? [...currentEdge, flow] : [flow];
    this.transitions.connect(config.output.classId, config.input.classId, edge);
  }

  public getFlows(
    from: ClassId,
    fromOutput: OutputName,
    to: Array<ClassId>,
    toInput: InputName | undefined,
  ): Array<Flow> {
    const flows = to.reduce(
      (acc, classId) => [...acc, ...(this.transitions.getEdge(from, classId) || [])],
      [] as Array<Flow>,
    );

    return flows.filter(
      flow =>
        flow.output === fromOutput && (toInput === undefined || flow.input === undefined || toInput === flow.input),
    );
  }
}
