import { InjectionToken } from '@angular/core';
import { Property } from '@backbase/foundation-ang/web-sdk';
import { ItemModel } from '../../../models/item-model';
import { PipelineSource, Source } from '../pipeline-interface';
import { PipelineProcessorConfig } from '../pipeline-registry';

/**
 * A `PipelineProcessor` which can be used as a source for a component's
 * input.
 *
 * See `modelProcessorConfig` for configuration.
 */
export interface ModelSource extends PipelineSource<Property | undefined> {
  /**
   * @param propertyName the name of the model property to read from
   */
  source: (propertyName: string) => Source<Property | undefined>;
}

export const createModelSource = (itemModel: ItemModel): PipelineSource<Property | undefined> => ({
  source: (propertyName: string) => () => itemModel.property(propertyName),
});

/**
 * Configure the `ModelSource`.
 *
 * Use the string 'model' to configure this handler.
 *
 * @usageNotes
 *
 * ```json
 * {
 *   "modelProperty": "foobar",
 *   "input.myInput": "model modelProperty", // read the value of modelProperty
 * }
 * ```
 */
export const modelProcessorConfig: PipelineProcessorConfig<'model'> = {
  name: 'model',
  provider: {
    provide: new InjectionToken('Model pipeline handler'),
    useFactory: createModelSource,
    deps: [ItemModel],
  },
};
