import { Routes, Route, PRIMARY_OUTLET } from '@angular/router';
import { OutletCollisionComponent } from '../components/outlet-collision.component';
import { flatMap } from '../../models/array';
import { Tree } from '../../models/tree';
import { OutletName, OutletCollision } from './outlet-map';
import { RoutableItem } from './routable-item-tree';
import { ItemName } from '../../models/item-model';

export type ComponentRouteTree = Tree<ItemRoutes>;

/**
 * An ItemRoute represents a node in a routing tree, that is a
 * direct map of the CXP tree.
 *
 * The routes for each node represents the entire route tree from
 * this item down (this item's routes, merged with child routes).
 */
export interface ItemRoutes {
  item: ItemName;
  routes: Routes;
  outletName?: OutletName;
}

export const isRoutable = (itemRoutes: ItemRoutes): boolean => itemRoutes.outletName !== undefined;

const isAbsolute = (routePath: string) => routePath.startsWith('/');

const isMatchingRedirect = (redirectTo: string) => (route: Route): boolean =>
  route.path !== undefined && route.path === redirectTo;

/**
 * Applies an outlet name to a route
 * @param outlet
 * @param route
 * @param oldRoutes
 */
const namedOutletRoute = (outlet: string, route: Route, oldRoutes: Routes): Route => {
  // Add outlet name
  let newRoute: Route = route.component ? { ...route, outlet } : route;

  // Convert redirects to duplicated routes (can't redirect to a named outlet easily).
  if (newRoute.redirectTo !== undefined && !isAbsolute(newRoute.redirectTo)) {
    // If it's not absolute, we try and make it a default route instead (no path)
    const routeTo = oldRoutes.find(isMatchingRedirect(newRoute.redirectTo));
    if (!routeTo) {
      // Coudn't find a route that they're redirecting too, so we remove the route, as it won't work.
      throw new Error(`Couldn't redirect to ${newRoute.redirectTo}`);
    }
    newRoute = {
      ...routeTo, // @fixme: child routes with empty path redirects won't actually do the redirect
      outlet, // ... that means a named deck container with a routable widget, won't show that
      path: '', // ... widget's first route if it's a redirect
    };
  }

  return newRoute;
};

const applyOutletName = (item: RoutableItem, outlet: OutletName, routes: Routes): Routes => {
  if (outlet === PRIMARY_OUTLET) {
    return routes;
  } else if (outlet instanceof OutletCollision) {
    return [
      {
        path: '',
        component: OutletCollisionComponent,
        outlet: outlet.outletName,
        data: {
          item: item.name,
          collisionWith: outlet.collisionWith,
        },
      },
    ];
  }

  return routes.map((route, _, oldRoutes) => namedOutletRoute(`${outlet}`, route, oldRoutes));
};

const mapItemToRoutes = (outlets: Map<ItemName, OutletName>) => (
  item: RoutableItem,
  children: Array<RoutableItem>,
  childRoutes: Array<ItemRoutes>,
): ItemRoutes => {
  const outletName = outlets.get(item.name);
  const routes = item.routingStrategy ? item.routingStrategy.getRoutes(item, children, childRoutes) : undefined;

  if (outletName === undefined || routes === undefined) {
    // Flatten non-routable items
    return {
      item: item.name,
      outletName: undefined,
      routes: flatMap(childRoute => childRoute.routes, childRoutes),
    };
  }
  return {
    item: item.name,
    routes: applyOutletName(item, outletName, routes),
    outletName,
  };
};

/**
 * Create a tree of the components with routes. This is a direct map of the
 * component tree. Each node in the tree holds all routes for the nodes beneath
 * it. So the root node in the returned tree will hold all the routes for the
 * entire component tree.
 */
export const routingTree = (tree: Tree<RoutableItem>, outlets: Map<ItemName, OutletName>): ComponentRouteTree =>
  tree.map(mapItemToRoutes(outlets));
