import { Injectable, Injector } from '@angular/core';
import { PipelineRegistry } from './pipeline-registry';
import {
  PipelinePipe,
  Pipe,
  Sink,
  Pipeline,
  PipelineSink,
  Source,
  PipelineSource,
  PipelineProcessor,
} from './pipeline-interface';
import { Observable } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { pipeFromArray } from '../../models/observable';

const noop = () => {};

const noopSink = <R>(stdin: Observable<R>) => {
  stdin.subscribe(noop);
};

const isSourceDefinition = <T, R>(definition: PipelineProcessor<T, R>): definition is PipelineSource<T> =>
  definition && 'source' in definition;

const isPipeDefinition = <T, R>(definition: PipelineProcessor<T, R>): definition is PipelinePipe<T, R> =>
  definition && 'pipe' in definition;

const isSinkDefinition = <R>(definition: PipelineProcessor<any, R>): definition is PipelineSink<R> =>
  definition && 'sink' in definition;
/**
 * @deprecated Will be removed in v7.0.0
 */
@Injectable()
export class PipelineService {
  constructor(private readonly broker: PipelineRegistry, private readonly injector: Injector) {}

  createSource<T>(name: string, args: Array<any>): Source<T> {
    const instance = this.getInstance<T, any>(name);
    if (!isSourceDefinition(instance)) {
      throw new Error(`${name} is not a valid source`);
    }
    return instance.source(...args);
  }

  createPipe<T, R>(name: string, args: Array<any>): Pipe<T, R> {
    const instance = this.getInstance<T, R>(name);
    if (!isPipeDefinition(instance)) {
      throw new Error(`${name} is not a valid pipe`);
    }
    return instance.pipe(...args);
  }

  createSink<R>(name: string, args: Array<any>): Sink<R> {
    const instance = this.getInstance<any, R>(name);
    if (isSinkDefinition(instance)) {
      return instance.sink(...args);
    } else if (isPipeDefinition(instance)) {
      return this.chainSink([instance.pipe(...args)], noopSink);
    }
    throw new Error(`${name} is not a valid sink`);
  }

  private getInstance<T, R>(name: string): PipelineProcessor<T, R> {
    const provide = this.broker.getProcessorProviderToken(name);
    return this.injector.get<PipelineProcessor<T, R>>(provide);
  }

  chainSink<T, R>(pipes: Array<Pipe<T, R>>, sink: Sink<R>): Sink<T> {
    return (stdin: Observable<T>): void => {
      sink(stdin.pipe(pipeFromArray(pipes)));
    };
  }

  runPipeline<T>(pipeline: Pipeline<T>, gc: Observable<any>): void {
    return pipeline.sink(pipeline.source().pipe(takeUntil(gc)));
  }
}
