import {
  AfterContentInit,
  ContentChildren,
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  QueryList,
  Renderer2,
  Optional,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { RouterLinkDirective, RouterLinkWithHrefDirective } from './router-link.directive';
import { Router, NavigationEnd, Event } from '@angular/router';

/**
 * Router link options
 */
export interface RouterLinkActiveOptions {
  /**
   * If it is `true`, the directive will add the classes only when the url matches the link exactly.
   * Default: `false`
   */
  exact?: boolean;
  /**
   * If it is defined, active link will get `aria-current` attribute set to the value provided and all
   * the other links will get `aria-current` set to `'false'`.
   * Default: `undefined`
   */
  ariaCurrent?: string;
}

/**
 *
 * @description
 *
 * Lets you add a CSS class and `aria-current` attribute to an element when the link's route becomes active.
 *
 * This directive lets you add a CSS class and `aria-current` attribute to an element when the link's route
 * becomes active.
 *
 * Consider the following example:
 *
 * ```
 * <a bbRouterLink="/user/bob" bbRouterLinkActive="active-link">Bob</a>
 * ```
 *
 * When the url is either '/user' or '/user/bob', the active-link class will
 * be added to the `a` tag. If the url changes, the class Will be removed.
 *
 * You can set more than one class, as follows:
 *
 * ```
 * <a bbRouterLink="/user/bob" bbRouterLinkActive="class1 class2">Bob</a>
 * <a bbRouterLink="/user/bob" [bbRouterLinkActive]="['class1', 'class2']">Bob</a>
 * ```
 *
 * You can configure RouterLinkActive by passing `exact: true`. This will add the classes
 * only when the url matches the link exactly.
 *
 * ```
 * <a bbRouterLink="/user/bob" bbRouterLinkActive="active-link" [bbRouterLinkActiveOptions]="{exact:
 * true}">Bob</a>
 * ```
 *
 * You can configure RouterLinkActive to set `aria-current` attribute for all links. If option is defined,
 * value will be added to the active link and `aria-current="false"` to the other links.
 *
 * ```
 * <a bbRouterLink="/user/bob" bbRouterLinkActive="active-link" [bbRouterLinkActiveOptions]="{ariaCurrent: 'page'}">
 *  Bob
 * </a>
 * ```
 *
 * You can assign the RouterLinkActive instance to a template variable and directly check
 * the `isActive` status.
 * ```
 * <a bbRouterLink="/user/bob" bbRouterLinkActive #rla="bbRouterLinkActive">
 *   Bob {{ rla.isActive ? '(already open)' : ''}}
 * </a>
 * ```
 *
 * Finally, you can apply the RouterLinkActive directive to an ancestor of a RouterLink.
 *
 * ```
 * <div bbRouterLinkActive="active-link" [bbRouterLinkActiveOptions]="{exact: true}">
 *   <a bbRouterLink="/user/jim">Jim</a>
 *   <a bbRouterLink="/user/bob">Bob</a>
 * </div>
 * ```
 *
 * This will set the active-link class on the div tag if the url is either '/user/jim' or
 * '/user/bob'.
 *
 * @ngModule RouterModule
 *
 */
@Directive({
  selector: '[bbRouterLinkActive]',
  exportAs: 'bbRouterLinkActive',
})
export class RouterLinkActiveDirective implements OnChanges, OnDestroy, AfterContentInit {
  @ContentChildren(RouterLinkDirective, { descendants: true })
  links: QueryList<RouterLinkDirective> | undefined;
  @ContentChildren(RouterLinkWithHrefDirective, { descendants: true })
  linksWithHrefs: QueryList<RouterLinkWithHrefDirective> | undefined;

  private classes: Array<string> = [];
  private readonly subscription: Subscription;
  public readonly isActive: boolean = false;

  @Input() bbRouterLinkActiveOptions: RouterLinkActiveOptions = {};

  constructor(
    private readonly router: Router,
    private readonly element: ElementRef,
    private readonly renderer: Renderer2,
    @Optional() private readonly link?: RouterLinkDirective,
    @Optional() private readonly linkWithHref?: RouterLinkWithHrefDirective,
  ) {
    this.subscription = router.events.subscribe((s: Event) => {
      if (s instanceof NavigationEnd) {
        this.update();
      }
    });
  }

  ngAfterContentInit(): void {
    if (!this.links || !this.linksWithHrefs) return;
    this.links.changes.subscribe(_ => this.update());
    this.linksWithHrefs.changes.subscribe(_ => this.update());
    this.update();
  }

  @Input()
  set bbRouterLinkActive(data: Array<string> | string) {
    const classes = Array.isArray(data) ? data : data.split(' ');
    this.classes = classes.filter(c => !!c);
  }

  ngOnChanges(): void {
    this.update();
  }
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  private updateClasses(hasActiveLinks: boolean) {
    this.classes.forEach(c => {
      if (hasActiveLinks) {
        this.renderer.addClass(this.element.nativeElement, c);
      } else {
        this.renderer.removeClass(this.element.nativeElement, c);
      }
    });
  }

  private updateAria(hasActiveLinks: boolean) {
    const ariaAttr = 'aria-current';
    if (this.bbRouterLinkActiveOptions.ariaCurrent) {
      this.renderer.setAttribute(
        this.element.nativeElement,
        ariaAttr,
        hasActiveLinks ? this.bbRouterLinkActiveOptions.ariaCurrent : 'false',
      );
    } else {
      this.renderer.removeAttribute(this.element.nativeElement, ariaAttr);
    }
  }

  private update(): void {
    if (!this.links || !this.linksWithHrefs || !this.router.navigated) return;
    this.hasActiveLinks().then(hasActiveLinks => {
      if (this.isActive !== hasActiveLinks) {
        (this as any).isActive = hasActiveLinks;
        this.updateAria(hasActiveLinks);
        this.updateClasses(hasActiveLinks);
      }
    });
  }

  private isLinkActive(router: Router): (link: RouterLinkDirective | RouterLinkWithHrefDirective) => Promise<boolean> {
    return (link: RouterLinkDirective | RouterLinkWithHrefDirective): Promise<boolean> =>
      link.urlTree.then(urlTree => router.isActive(urlTree, !!this.bbRouterLinkActiveOptions.exact));
  }

  private hasActiveLinks(): Promise<boolean> {
    if (!this.links || !this.linksWithHrefs) return Promise.resolve(false);
    return Promise.all([
      this.link ? this.isLinkActive(this.router)(this.link) : Promise.resolve(false),
      this.linkWithHref ? this.isLinkActive(this.router)(this.linkWithHref) : Promise.resolve(false),
      ...this.links.map(this.isLinkActive(this.router)),
      ...this.linksWithHrefs.map(this.isLinkActive(this.router)),
    ]).then((liveActives: Array<boolean>): boolean => liveActives.some(a => a));
  }
}
