import {
  Directive,
  HostBinding,
  Input,
  OnChanges,
  OnDestroy,
  HostListener,
  Attribute,
  Renderer2,
  ElementRef,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { Router, ActivatedRoute, NavigationEnd, UrlTree } from '@angular/router';
import { LocationStrategy } from '@angular/common';
import { RouterService } from '../navigation/router.service';

// This is declared in `@angular/router/src/config`, but doesn't seem to be exported
export type QueryParamsHandling = 'merge' | 'preserve' | '';

const toCommandsArray = (commands: Array<any> | string): Array<any> => {
  if (commands != null) {
    return Array.isArray(commands) ? commands : [commands];
  } else {
    return [];
  }
};

@Directive({ selector: 'a[bbRouterLink]' })
export class RouterLinkWithHrefDirective implements OnChanges, OnDestroy {
  @HostBinding('attr.target')
  @Input()
  target: string | undefined;

  @Input() queryParams: { [k: string]: any } | undefined;
  @Input() fragment: string | undefined;
  @Input() queryParamsHandling: QueryParamsHandling | undefined;
  @Input() preserveFragment: boolean | undefined;
  @Input() skipLocationChange: boolean | undefined;
  @Input() replaceUrl: boolean | undefined;

  private commands: Array<any> = [];
  private readonly subscriptions: Array<Subscription>;
  private readonly preserve: boolean | undefined;

  constructor(
    private readonly router: Router,
    private readonly bbRouter: RouterService,
    private readonly locationStrategy: LocationStrategy,
    private readonly el: ElementRef,
    private readonly renderer: Renderer2,
  ) {
    this.subscriptions = [
      router.events.subscribe(s => {
        if (s instanceof NavigationEnd) {
          this.updateTargetUrlAndHref();
        }
      }),
    ];
  }

  @Input()
  set bbRouterLink(commands: Array<any> | string) {
    this.commands = toCommandsArray(commands);
  }

  ngOnChanges() {
    this.updateTargetUrlAndHref();
  }

  ngOnDestroy() {
    this.subscriptions.forEach(s => s.unsubscribe());
  }

  @HostListener('click', ['$event.button', '$event.ctrlKey', '$event.metaKey', '$event.shiftKey'])
  onClick(button: number, ctrlKey: boolean, metaKey: boolean, shiftKey: boolean): boolean {
    if (button !== 0 || ctrlKey || metaKey || shiftKey) {
      return true;
    }

    if (typeof this.target === 'string' && this.target !== '_self') {
      return true;
    }

    const extras = {
      skipLocationChange: attrBoolValue(this.skipLocationChange),
      replaceUrl: attrBoolValue(this.replaceUrl),
    };
    this.urlTree.then(urlTree => {
      this.router.navigateByUrl(this.router.serializeUrl(urlTree), extras);
    });
    return false;
  }

  private updateTargetUrlAndHref(): void {
    this.urlTree.then(urlTree => {
      this.renderer.setAttribute(
        this.el.nativeElement,
        'href',
        this.locationStrategy.prepareExternalUrl(this.router.serializeUrl(urlTree)),
      );
    });
  }

  get urlTree(): Promise<UrlTree> {
    return this.bbRouter.createUrlTree(this.commands, {
      queryParams: this.queryParams,
      fragment: this.fragment,
      ...(attrBoolValue(this.preserve) ? { queryParamsHandling: 'preserve' } : {}),
      queryParamsHandling: this.queryParamsHandling,
      preserveFragment: attrBoolValue(this.preserveFragment),
    });
  }
}

@Directive({ selector: ':not(a)[bbRouterLink]' })
export class RouterLinkDirective {
  @Input() queryParams: { [k: string]: any } | undefined;
  @Input() fragment: string | undefined;
  @Input() queryParamsHandling: QueryParamsHandling | undefined;
  @Input() preserveFragment: boolean | undefined;
  @Input() skipLocationChange: boolean | undefined;
  @Input() replaceUrl: boolean | undefined;
  private commands: Array<any> = [];
  private readonly preserve: boolean | undefined;

  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute,
    @Attribute('tabindex') tabIndex: string,
    renderer: Renderer2,
    el: ElementRef,
    private readonly bbRouter: RouterService,
  ) {
    if (tabIndex == null) {
      renderer.setAttribute(el.nativeElement, 'tabindex', '0');
    }
  }

  @Input()
  set bbRouterLink(commands: Array<any> | string) {
    this.commands = toCommandsArray(commands);
  }

  @HostListener('click')
  onClick(): boolean {
    const extras = {
      skipLocationChange: attrBoolValue(this.skipLocationChange),
      replaceUrl: attrBoolValue(this.replaceUrl),
    };

    this.urlTree.then(urlTree => {
      this.router.navigateByUrl(this.router.serializeUrl(urlTree), extras);
    });
    return true;
  }

  get urlTree(): Promise<UrlTree> {
    return this.bbRouter.createUrlTree(this.commands, {
      relativeTo: this.route,
      queryParams: this.queryParams,
      fragment: this.fragment,
      ...(attrBoolValue(this.preserve) ? { queryParamsHandling: 'preserve' } : {}),
      queryParamsHandling: this.queryParamsHandling,
      preserveFragment: attrBoolValue(this.preserveFragment),
    });
  }
}

function attrBoolValue(s: any): boolean {
  return s === '' || !!s;
}
