import {
  ComponentRef,
  ViewContainerRef,
  ComponentFactoryResolver,
  ComponentFactory,
  Injectable,
  Inject,
  InjectionToken,
} from '@angular/core';

/**
 * The session timeout configuration.
 * Allows the application to manage the session timeout component class and inactivity modal time.
 */
export interface SessionTimeoutConfig {
  /**
   * The name of the component to be used for stepup mechanism
   */
  sessionTimeoutComponentClass: SessionTimeoutComponentType;
  /**
   * The time before session expiry that the modal should appear in seconds
   */
  inactivityModalTime: number;
}

export const SESSION_TIMEOUT_CONFIG = new InjectionToken<SessionTimeoutConfig>(
  'Custom session timeout configuration injection token',
);

/**
 * The interface for a SessionTimeoutComponent
 */
export interface SessionTimeoutComponent {
  /**
   * The default time in seconds to open the session timeout modal before session expiry.
   * This will be overwritten if you set `maxInactivityDuration` in the experience configuration
   */
  inactivityModalTime: number;
}

/**
 * The session timeout component type, set to SessionTimeoutComponent
 */
export type SessionTimeoutComponentType = new (...args: Array<any>) => SessionTimeoutComponent;

/**
 * The session timeout service creates a session timeout component instance within the root.
 */
@Injectable()
export class SessionTimeoutService {
  private sessionTimeoutComponentRef: ComponentRef<SessionTimeoutComponent> | undefined;

  constructor(
    private readonly resolver: ComponentFactoryResolver,
    @Inject(SESSION_TIMEOUT_CONFIG) private readonly sessionTimeoutConfig: SessionTimeoutConfig,
  ) {}

  get sessionTimeoutComponent(): ComponentRef<SessionTimeoutComponent> {
    if (this.sessionTimeoutComponentRef) {
      return this.sessionTimeoutComponentRef;
    }
    throw new Error('No sessionTimeoutComponent defined');
  }

  createSessionTimeoutComponent(sessionTimeoutContainer: ViewContainerRef) {
    sessionTimeoutContainer.clear();
    const factory: ComponentFactory<SessionTimeoutComponent> = this.resolver.resolveComponentFactory(
      this.sessionTimeoutConfig.sessionTimeoutComponentClass,
    );
    this.sessionTimeoutComponentRef = sessionTimeoutContainer.createComponent(factory);
    this.sessionTimeoutComponentRef.instance.inactivityModalTime = this.sessionTimeoutConfig.inactivityModalTime;
  }
}
